<template>
	<div class="fitler_container">
		<!-- item -->
		<div class="food_filter border-bottom">
			<div class="item_wrap">
				<div class="item" :class="{ active: activeIndex === 1 }" @click="handleItemClick(1)">
					<span v-if="activeIndex === 1" class="name">分类</span>
					<span v-else class="name">{{ categoryNameCopy }}</span>
					<i class="iconfont arrow_icon">&#xe6aa;</i>
				</div>
				<div class="item border-left" :class="{ active: activeIndex === 2 }" @click="handleItemClick(2)">
					<span class="name">排序</span>
					<i class="iconfont arrow_icon">&#xe6aa;</i>
				</div>
				<div class="item border-left" :class="{ active: activeIndex === 3 }" @click="handleItemClick(3)">
					<span class="name">筛选</span>
					<i class="iconfont arrow_icon">&#xe6aa;</i>
				</div>
			</div>
		</div>
		<!-- content -->
		<div class="category_content">
			<!-- 分类 -->
			<div :class="{ active: activeIndex === 1 }" class="category_container category_sort">
				<div class="sort_left">
					<div 
						class="item"
						:class="{ active: categoryActiveIndex === (index + 1) }"
						v-for="(item, index) in foodCategoryList"
						:key="item.id"
						@click="handleCategoryClick(index + 1, item.id)"
					>
						<span class="name">
							<img :src="getImgPath(item.image_url)" alt="">
							{{ item.name }}
						</span>
						<span class="nums">
							<span class="num_icon">{{ item.count }}</span>
							<i class="iconfont">&#xe628;</i>
						</span>
					</div>
				</div>
				<div class="sort_right" v-if="foodCategoryList.length">
					<div 
						class="item border-bottom"
						v-for="(item, index) in foodCategoryList[categoryActiveIndex - 1].sub_categories"
						:key="item.id"
						v-if="index > 0"
						@click="handleCategoryDetailClick(item.id, item.name)"
					>
						<span>{{ item.name }}</span>
						<span class="total">{{ item.count }}</span>
					</div>
				</div>
			</div>
			<!-- 排序 -->
			<div :class="{ active: activeIndex === 2 }" class="category_container sort_way">
				<ul>
					<li class="item">
						<svg class="sort_icon">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#default"></use>
						</svg>
						<div class="desc border-bottom" :class="{ active: sortByType === 0 }" @click="handleSortCLick(0)">
							<span>智能排序</span>
							<svg class="sel_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
							</svg>
						</div>
					</li>
					<li class="item">
						<svg class="sort_icon">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#distance"></use>
						</svg>
						<div class="desc border-bottom" :class="{ active: sortByType === 5 }" @click="handleSortCLick(5)">
							<span>距离最近</span>
							<svg class="sel_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
							</svg>
						</div>
					</li>
					<li class="item">
						<svg class="sort_icon">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#hot"></use>
						</svg>
						<div class="desc border-bottom" :class="{ active: sortByType === 6 }" @click="handleSortCLick(6)">
							<span>销量最高</span>
							<svg class="sel_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
							</svg>
						</div>
					</li>
					<li class="item">
						<svg class="sort_icon">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#price"></use>
						</svg>
						<div class="desc border-bottom" :class="{ active: sortByType === 1 }" @click="handleSortCLick(1)">
							<span>起送价最低</span>
							<svg class="sel_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
							</svg>
						</div>
					</li>
					<li class="item">
						<svg class="sort_icon">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#speed"></use>
						</svg>
						<div class="desc border-bottom" :class="{ active: sortByType === 2 }" @click="handleSortCLick(2)">
							<span>配送速度最快</span>
							<svg class="sel_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
							</svg>
						</div>
					</li>
					<li class="item">
						<svg class="sort_icon">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#rating"></use>
						</svg>
						<div class="desc border-bottom" :class="{ active: sortByType === 3 }" @click="handleSortCLick(3)">
							<span>评分最高</span>
							<svg class="sel_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
							</svg>
						</div>
					</li>
				</ul>
			</div>
			<!-- 筛选 -->
			<div :class="{ active: activeIndex === 3 }" class="category_container filter_way">
				<div class="choose_container">
					<h4>配送方式</h4>
					<ul class="list">
						<li 
							class="item_btn border"
							:class="{ active: deliveryMode === item.id }"
							v-for="item in deliveryList" 
							:key="item.id" 
							@click="handleDeliveryClick(item.id)"
						>
							<svg class="svg_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="deliveryMode === item.id ? '#selected' : '#fengniao'"></use>
							</svg>
							<span class="text">{{ item.text }}</span>
						</li>
					</ul>
					<h4>商家属性（可以多选）</h4>
					<ul class="list">
						<li 
							class="item_btn border" 
							:class="{ active: activitySelectedList.indexOf(item.id) !== -1 }"
							v-for="item in activityList" 
							:key="item.id"
							@click="handleActivityClick(item.id)"
						>
							<svg v-if="activitySelectedList.indexOf(item.id) !== -1" class="svg_icon">
								<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#selected"></use>
							</svg>
							<span v-else class="attr_icon" :style="{ color: '#' + item.icon_color, borderColor: '#' + item.icon_color }">{{ item.icon_name }}</span>
							<span class="text">{{ item.name }}</span>
						</li>
					</ul>
				</div>
				<div class="btns">
					<span class="clear_btn" @click="clearFilterItems">清空</span>
					<span class="confirm_btn" @click="handleFilterClick">确定<span v-show="filterSelectedNums">({{ filterSelectedNums }})</span></span>
				</div>
			</div>
		</div>
		<!-- cover -->
		<div v-show="activeIndex" class="cover"></div>
	</div>
</template>

<script>
import { getImgPath } from '@/mixins'
import { getFoodCategory, getMsiteAddress, getFoodDelivery, getFoodActivity } from '@/ajaxApi/getData'
import { mapState, mapMutations } from 'vuex'
export default {
	name: 'FoodFilter',
	props: {
		geohash: String,
		categoryName: String,
		restaurantCategoryId: [String, Number],		// 获取从父组件传来的分类id

	},
	data () {
		return {
			activeIndex: 0,
			foodCategoryList: [],
			categoryActiveIndex: 1,
			categoryNameCopy: this.categoryName,
			restaurantCategoryIdCopy: this.restaurantCategoryId,	// 分类id的副本
			sortByType: '',		// 记录排序方式
			deliveryList: [],	// 配送方式
			deliveryMode: '',	
			activityList: [],	// 活动列表
			activitySelectedList: [],
		}
	},
	computed: {
		...mapState([ 'latitude', 'longitude' ]),
		filterSelectedNums () {
			let mode = this.deliveryMode !== '' ? 1 : 0
			return this.activitySelectedList.length + mode
		}
	},
	mixins: [getImgPath],
	methods: {
		...mapMutations([ 'RECORD_ADDRESS' ]),
		// 
		handleItemClick (index) {
			if (this.activeIndex === index) {
				this.activeIndex = 0
				return
			}
			this.activeIndex = index
		},
		// 获取分类左侧数据成功
		getFoodCategorySucc (res) {
			res = res.data
			// console.log(res)
			this.foodCategoryList = res
			// 根据分类id选中指定的项
			// console.log(this.restaurantCategoryId)
			for (let i = 0; i < this.foodCategoryList.length; i++) {
				if (this.foodCategoryList[i].id === this.restaurantCategoryId) {
					this.categoryActiveIndex = i + 1
					break
				}
			}
		},
		// 点击分类左侧切换右侧列表数据
		handleCategoryClick (index, id) {
			this.categoryActiveIndex = index
			if (id) {
				this.restaurantCategoryIdCopy = id
			} else {
				// 若id不存在 表示查询所有数据

			}
		},
		// 点击分类右侧项后重新筛选数据
		handleCategoryDetailClick (id, name) {
			this.activeIndex = 0
			this.categoryNameCopy = name
			// 像父组件传递数据
			let dataObj = {
				cateName: this.categoryNameCopy,
				// cateId: this.restaurantCategoryIdCopy,
				cateIds: id
			}
			this.$emit('filterCate', dataObj)
		},
		// 点击排序方式重新筛选数据
		handleSortCLick (num) {
			this.activeIndex = 0
			// console.log(num)
			this.sortByType = num
			// 像父组件传递数据
			let dataObj = {
				sortByType: this.sortByType
			}
			this.$emit('filterCate', dataObj)
		},
		// 选择配送方式(单选)
		handleDeliveryClick (id) {
			if (this.deliveryMode === id) {
				this.deliveryMode = ''
				return
			}
			this.deliveryMode = id
		},
		// 选择商家属性(多选)
		handleActivityClick (id) {
			if (this.activitySelectedList.indexOf(id) === -1) {		// 新增
				this.activitySelectedList.push(id)
			} else {	// 删除
				let index = this.activitySelectedList.indexOf(id)
				this.activitySelectedList.splice(index, 1)
			}
			// console.log(this.activitySelectedList)
		},
		// 清空已选择的筛选项
		clearFilterItems () {
			this.deliveryMode = ''
			this.activitySelectedList = []
		},
		// 点击确定按照筛选项重新筛选数据
		handleFilterClick () {
			this.activeIndex = 0
			// 向父组件传递参数
			let dataObj = {
				supportWays: {
					deliveryMode: this.deliveryMode,
					activityList: this.activitySelectedList
				}
			}
			this.$emit('filterCate', dataObj)
		}
	},
	async mounted () {
		// 获取当前的经纬度 (防止页面刷新vuex里的数据丢失)
		if (!this.latitude) {
			let res = await getMsiteAddress(this.geohash)
			res = res.data
			// console.log(res)
			this.RECORD_ADDRESS(res)
		}
		// console.log(this.latitude)
		// 获取分类左侧数据
		getFoodCategory(this.latitude, this.longitude).then(this.getFoodCategorySucc)
		// 获取筛选类数据(配送方式, 商家属性)
		getFoodDelivery(this.latitude, this.longitude)
			.then((res) => {
				res = res.data
				// console.log(res)
				this.deliveryList = res
			})
		getFoodActivity(this.latitude, this.longitude)
			.then((res) => {
				res = res.data
				// console.log(res)
				this.activityList = res
			})
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	
	.border-bottom:before,
	.border-left:before,
	.border:before {
		border-color: $borderColor;
	}
	
	.fitler_container {
		position: relative;
		top: .92rem;
	}
	.food_filter {
		position: fixed;
		width: 100%;
		height: .75rem;
		z-index: 80;
		top: .92rem;
		left: 0;
		background: #fff;
		.item_wrap {
			// position: relative;
			// z-index: 10;
			padding: .14rem 0;
			height: 100%;
			display: flex;
			align-items: center;
			.item {
				flex: 1;
				height: .4rem;
				line-height: .4rem;
				text-align: center;
				color: #333;
				.name {
					
				}
				.arrow_icon {
					display: inline-block;
					transform: translateY(.04rem);
					transition: transform .6s;
					transform: rotate(0);
				}
			}
			.item.active {
				color: $blueColor;
				transition: color .6s;
				.arrow_icon {
					// transition: transform .6s;
					transform: rotate(180deg);
				}
			}
		}
	}
	.category_content {
		position: absolute;
		width: 100%;
		z-index: 70;
		top: .75rem;
		left: 0;
	}
	/*  */
	.category_container {
		position: absolute;
		width: 100%;
		/* display: none; */
		transition: all .6s;
		transform: translateY(-100%);
		opacity: 0;
		min-height: 4rem;
		background: #fff;
	}
	.category_container.active {
		/* display: block; */
		transform: translateY(0);
		opacity: 1;
	}
	/* 分类 */
	.category_sort {
		display: flex;
		.sort_left, .sort_right {
			height: 7.56rem;
			overflow-y: auto;
		}
		.sort_left {
			width: 3.6rem;
			.item {
				padding: 0 .24rem;
				background: #f5f5f5;
				height: .84rem;
				line-height: .84rem;
				font-size: .24rem;
				display: flex;
				justify-content: space-between;
				.name {
					display: flex;
					align-items: center;
					img {
						width: .4rem;
						height: .4rem;
						margin-right: .1rem;
					}
				}
				.nums {
					color: #999;
					display: flex;
					align-items: center;
					.num_icon {
						display: inline-block;
						height: .32rem;
						line-height: .32rem;
						background: #ccc;
						padding: 0 .06rem;
						color: #fff;
						font-weight: bold;
						border-radius: .32rem;	
					}
				}
			}
			.item.active {
				background: #fff;
		
			}
		}
		.sort_right {
			flex: 1;
			padding-left: .24rem;
			.item {
				height: .84rem;
				line-height: .84rem;
				padding-right: .24rem;
				display: flex;
				justify-content: space-between;

			}
		}
	}
	/* 排序 */
	.sort_way {
		.item {
			height: 1.2rem;
			line-height: 1.2rem;
			padding-left: .36rem;
			display: flex;
			align-items: center;
			.sort_icon {
				margin-right: .14rem;
				width: .32rem;
				height: .32rem;
			}
			.desc {
				flex: 1;
				font-size: .26rem;
				display: flex;
				justify-content: space-between;
				align-items: center;
				.sel_icon {
					margin-right: .14rem;
					width: .32rem;
					height: .32rem;
					display: none;
				}
			}
			.desc.active {
				span {
					color: $blueColor;
				}
				.sel_icon {
					display: inline-block;
				}
			}
		}
	}
	/* 筛选 */
	.filter_way {
		color: #333;
		.choose_container {
			padding: 0 .24rem .24rem .24rem;
			h4 {
				height: .7rem;
				line-height: .7rem;
				font-size: .24rem;
			}
			.list {
				font-size: 0;
				.item_btn {
					display: inline-block;
					min-width: 2.2rem;
					padding: 0 .12rem;
					height: .64rem;
					line-height: .64rem;
					border-radius: .04rem;
					margin-right: .14rem;
					margin-bottom: .14rem;
					font-size: 0;
					.svg_icon {
						width: .36rem;
						height: .36rem;
						margin-right: .06rem;
						margin-bottom: -.08rem;
					}
					.text {
						font-size: .24rem;
					}
					.attr_icon {
						display: inline-block;
						font-size: .24rem;
						width: .36rem;
						height: .36rem;
						line-height: .32rem;
						margin-right: .06rem;
						// margin-bottom: -.08rem;
						border: solid .02rem #e1e1e1;
						border-radius: .06rem;
						text-align: center;
					}
				}
				.item_btn.active {
					.text {
						color: $blueColor
					}
				}
			}
		}
		.btns {
			padding: .14rem .1rem;
			background: #f1f1f1;
			display: flex;
			.clear_btn, .confirm_btn {
				flex: 1;
				height: .84rem;
				line-height: .84rem;
				border-radius: .06rem;
				font-size: .36rem;
				text-align: center;
			}
			.clear_btn {
				background: #fff;
				margin-right: .24rem;
			}
			.confirm_btn {
				background: #56d176;
				color: #fff;
			}
		}
	}

	/* 遮罩层 */
	.cover {
		position: fixed;
		z-index: 10;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(0, 0, 0, .3);
	}
</style>


