<template>
	<div>
		<header-top showBack="true" headTitle="重置密码"></header-top>
		<div class="login_content">
			<!-- 输入区域 -->
			<form class="login_form">
				<div class="input_wrap border-bottom">
					<input type="text" placeholder="账号" v-model.lazy.trim="userAccount">
				</div>
				<div class="input_wrap border-bottom">
					<input type="text" placeholder="旧密码" v-model="oldPassWord">
				</div>
				<div class="input_wrap border-bottom">
					<input type="text" placeholder="请输入新密码" v-model="newPassWord">
				</div>
				<div class="input_wrap border-bottom">
					<input type="text" placeholder="请确认密码" v-model="confirmPassWord">
				</div>
				<!-- 验证码 -->
				<div class="input_wrap border-bottom">
					<input type="text" placeholder="验证码" v-model="verifyCode">
					<div class="change_code_img">
						<img v-show="codeImgUrl" :src="codeImgUrl" alt="">
						<div class="change_code" @click="handleGetVerifyCode">
							<p>看不清</p>
							<p>换一张</p>
						</div>
					</div>
				</div>
			</form>
			<!-- 确认修改 -->
			<div class="submit_btn" @click="handleResetPassword">确认修改</div>
		</div>
		<!-- 提示弹框 -->
		<alert-tip v-if="showAlertTip" @closeTip="handleCloseTip" :tipText="alertTipText"></alert-tip>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
import AlertTip from '@/components/common/AlertTip'
import { getVerifyCode, resetLoginPassword } from '@/ajaxApi/getData'
import { mapMutations } from 'vuex'
export default {
	name: 'Forget',
	components: {
		HeaderTop,
		AlertTip
	},
	data () {
		return {
			userAccount: '',
			oldPassWord: '',
			newPassWord: '',
			confirmPassWord: '',
			verifyCode: '',
			codeImgUrl: null,
			showAlertTip: false,
			alertTipText: '',
			userInfo: null,	// 获取到的用户信息
			modifySucc: false
		}
	},
	methods: {
		...mapMutations(['RECORD_USERINFO']),
		changePassType () {
			this.showPassword = !this.showPassword;
		},
		// 获取验证吗，线上环境使用固定的图片，生产环境使用真实的验证码
		async handleGetVerifyCode () {
			let codeImg = await getVerifyCode()
			codeImg = codeImg.data.code
			this.codeImgUrl = codeImg
		},
		// 重置密码
		async handleResetPassword () {
			if (!this.userAccount) {
				this.showAlertTip = true
				this.alertTipText = '请输入正确的账号'
				return
			}
			if (!this.oldPassWord) {
				this.showAlertTip = true
				this.alertTipText = '请输入旧密码'
				return
			}
			if (!this.newPassWord) {
				this.showAlertTip = true
				this.alertTipText = '请输入新密码'
				return
			}
			if (!this.confirmPassWord) {
				this.showAlertTip = true
				this.alertTipText = '请输入确认密码'
				return
			}
			// 验证两次输入的密码是否一致
			if (this.confirmPassWord != this.newPassWord) {
				this.showAlertTip = true
				this.alertTipText = '两次输入的密码不一致'
				return
			}
			if (!this.verifyCode) {
				this.showAlertTip = true
				this.alertTipText = '请输入验证码'
				return
			}
			// 重置密码
			let res = await resetLoginPassword(this.userAccount, this.oldPassWord, this.newPassWord, this.confirmPassWord, this.verifyCode)
			res = res.data
			console.log(res)
			if (!res.success) {	// 修改失败
				this.showAlertTip = true
				this.alertTipText = res.message
				this.handleGetVerifyCode()	// 更新验证码
				return
			}
			// 修改成功
			this.showAlertTip = true
			this.alertTipText = res.success
			this.modifySucc = true
			
		},
		// 关闭提示弹框
		handleCloseTip () {
			this.showAlertTip = false
			this.alertTipText = ''
			// 若修改成功 回到上一页
			if (this.modifySucc) {
				this.$router.go(-1)
			}
		}
	},
	created () {
		// 获取验证码
		this.handleGetVerifyCode()
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	.border-bottom {
		&:before {
			border-color: $borderColor;
		}
	}

	.login_content {
		padding-top: 1.12rem;
		.login_form {
			background: #fff;
			.input_wrap {
				padding: .28rem .38rem;
				display: flex;
				justify-content: space-between;
				input {
					// width: 100%;
					font-size: .32rem;
				}
				.change_code_img {
					display: flex;
					align-items: center;
					height: .38rem;
					img {
						width: 1.64rem;
						height: .7rem;
						margin-right: .1rem;
					}
					.change_code {
						p {
							font-size: .24rem;
						}
						p:nth-child(2) {
							color: $blueColor;
							margin-top: .1rem;
						}
					}
				}
			}
		}
		.login_tips {
			padding: .18rem .28rem;
			font-size: .24rem;
			color: red;
			line-height: 1;
		}
		.submit_btn {
			margin: .48rem .24rem;
			padding: .24rem 0;
			line-height: .42rem;
			border-radius: .06rem;
			background: #4cd964;
			font-size: .32rem;
			color: #fff;
			text-align: center;
		}
	}
</style>


