<template>
	<div>
		<header-top showBack="true" :headTitle="currentCity">
			<router-link to="/" slot="changeCity" class="header_change">切换城市</router-link>
		</header-top>
		<div class="content">
			<city-serach @search="handleSearch"></city-serach>
			<city-search-list v-if="showSearchList" :list="searchList" :historyList="historyList" :showHistory="showHistory"></city-search-list>
		</div>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
import CitySerach from './components/Search'
import CitySearchList from './components/SearchList'
import { getCityById, getCitySearchList } from '@/ajaxApi/getData'
import { getStore } from '@/config/utils'
export default {
	name: 'City',
	components: {
		HeaderTop,
		CitySerach,
		CitySearchList
	},
	data () {
		return {
			currentCity: '',
			currentCityId: '',
			searchList: [],
			historyList: [],
			showHistory: true,
			showSearchList: false
		}
	},
	methods: {
		// 获取历史记录
		getHistoryList () {
			let history = getStore('historyList')
			console.log(history)
			if (!history) {
				this.showSearchList = true
				return
			}
			this.historyList = JSON.parse(history)
			this.showSearchList = true
		},
		// 根据关键字获取搜索项
		handleSearch (keyWords) {
			// console.log(keyWords)
			getCitySearchList(this.currentCityId, keyWords).then(this.handleSearchSucc)
		},
		handleSearchSucc (res) {
			res = res.data
			// console.log(res)
			this.searchList = res
			this.showHistory = false
		}
	},
	mounted () {
		this.currentCityId = this.$route.params.id
		// 获取城市信息
		getCityById(this.currentCityId).then((res) => {
			res = res.data
			this.currentCity = res.name
		})
		// 获取历史记录
		this.getHistoryList()
	}
}
</script>

<style lang="scss" scoped>
	@import '~style/mixin';

	.header_change {
		color: #fff;
		font-size: .32rem;
	}

	.content {
		padding-top: .92rem;
	}

</style>


