<template>
	<div class="shop_header">
		<i class="iconfont back_icon" @click="$router.go(-1)">&#xe624;</i>
		<img class="fuzzy_img" :src="imgBaseUrl + shopDetailsData.image_path" alt="">
		<div class="content_wrap">
			<div class="content_desc">
				<div class="left_img">
					<img :src="imgBaseUrl + shopDetailsData.image_path" alt="">
				</div>
				<div class="desc">
					<div class="desc_left">
						<h3>{{ shopDetailsData.name }}</h3>
						<p class="mar_bot">商家配送／{{ shopDetailsData.order_lead_time }}分钟送达／配送费¥{{ shopDetailsData.float_delivery_fee }}</p>
						<p>公告: {{ promotionInfo }}</p>
					</div>
					<div class="desc_right">
						<i class="iconfont right_icon">&#xe628;</i>
					</div>
				</div>
			</div>
			<div v-if="shopDetailsData.activities.length" class="content_activity">
				<p>
					<span class="act_icon" :style="{ backgroundColor:  '#' + shopDetailsData.activities[0].icon_color }">{{ shopDetailsData.activities[0].icon_name }}</span>
					<span>{{ shopDetailsData.activities[0].description }}（APP专享）</span>
				</p>
				<div class="right_act">
					<span>{{ shopDetailsData.activities.length }}个活动</span>
					<i class="iconfont">&#xe628;</i>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'ShopHeader',
	props: {
		shopDetailsData: Object
	},
	data () {
		return {
			imgBaseUrl: '//elm.cangdu.org/img/'
		}
	},
	computed: {
		// 公告信息
		promotionInfo () {
			return this.shopDetailsData.promotion_info || '欢迎光临，用餐高峰期请提前下单，谢谢.'
		}
	},
	methods: {

	},
	mounted () {
		
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';

	.shop_header {
		position: relative;
		z-index: 10;
		overflow: hidden;
		.back_icon {
			position: absolute;
			z-index: 15;
			font-weight: 800;
		    color: #fff;
		    font-size: .36rem;
		    left: .12rem;
		    top: .12rem;
		}
		.fuzzy_img {
			position: absolute;
			width: 100%;
			left: 0;
			top: 0;
			z-index: 11;
			filter: blur(10px);	
		}
		.content_wrap {
			background: rgba(119,103,137,.43);
			padding: .2rem 0 .2rem .2rem;
			position: relative;
			z-index: 12;
			.content_desc {
				display: flex;
				.left_img {
					width: 1.36rem;
					height: 1.36rem;
					border-radius: .06rem;
					overflow: hidden;
					margin-right: .1rem;
					img {
						width: 100%;
						min-height: 100%;
					}
				}
				.desc {
					min-width: 0;
					flex: 1;
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding-right: .1rem;
					.desc_left {
						min-width: 0;
						flex: 1;
						margin-right: .1rem;
						h3, p {
							@include ellipsis;
							color: #fff;
						}
						h3 {
							height: .6rem;
							line-height: .6rem;
							font-size: .36rem;
							font-weight: 800;
							margin-bottom: .06rem;
						}
						p {
							height: .36rem;
							line-height: .36rem;
							font-size: .24rem;
						}
						p.mar_bot {
							margin-bottom: .1rem;
						}
					}
					.desc_right {
						width: .4rem;
						text-align: center;
						.right_icon {
							font-size: .36rem;
							color: #fff;	
						}
					}
				}
			}
			.content_activity {
				margin-top: .2rem;
				display: flex;
				justify-content: space-between;
				align-items: center;
				color: #fff;
				font-size: .24rem;
				padding-right: .12rem;
				p, .right_act {
					line-height: .32rem;
				}
				p {
					.act_icon {
						display: inline-block;
						width: .32rem;
						height: .32rem;
						border-radius: .04rem;
						text-align: center;
					}
				}
				.right_act {
					display: flex;
					align-items: center;
				}
			}
		}
		
	}
</style>


