<template>
	<div class="list_container">
		<div class="content">
			<!-- 左侧菜单 -->
			<div class="left_nav" ref="leftNav">
				<ul>
					<li 
						class="nav_item border-bottom"
						:class="{ active: cateItemIndex == index }"
						v-for="(item, index) in shopLeftMenu"
						:key="item.id"
						@click="scrollToPos(index)"
					>
						<img v-show="item.icon_url" :src="getImgPath(item.icon_url)" alt="">
						<span class="name">{{ item.name }}</span>
						<span v-show="cateNumArr[index]" class="stati">{{ cateNumArr[index] }}</span>
					</li>
				</ul>
			</div>
			<!-- 右侧列表 -->
			<div class="right_list" ref="rightList">
				<ul>
					<li 
						class="cate_item"
						v-for="(item, index) in shopLeftMenu"
						:key="item.id"
						:id="'cateItem' + index"
					>
						<h4 class="title">
							<div class="text">
								<span class="name">{{ item.name }}</span>
								<span class="desc">{{ item.description }}</span>
							</div>
							<div class="point_icon" @click="showPointTips(index)">
								<div v-show="showPointTipIndex === index" class="point_icon_tip">
									<span class="tip_name">{{ item.name }}</span>
									<span class="tip_desc">{{ item.description }}</span>
								</div>
							</div>
						</h4>
						<ul class="item_list">
							<router-link
								tag="li"
								:to="{ path: '/shop/foodDetail', query: { name: food.name, image_path: food.image_path, rating: food.rating, month_sales: food.month_sales, description: food.description, rating_count: food.rating_count, satisfy_rate: food.satisfy_rate, food, shopId } }" 
								class="item"
								v-for="(food, index) in item.foods"
								:key="food.item_id"
							>	
								<!-- 是否为新品的标识 -->
								<div v-if="isShopNew(food.attributes)" class="attribute_new">
									<span>新品</span>
								</div>
								<div class="left_pic">
									<img :src="baseImgUrl + food.image_path" alt="">
								</div>
								<div class="right_con">
									<div class="desc">
										<!-- 商品属性 -->
										<ul v-if="food.attributes.length" class="attributes_ul">
											<li 
												v-if="attribute.icon_name != '新'"
												v-for="(attribute, index) in food.attributes"
												:key="'attribute' + index"
												class="attribute_item"
												:style="{ color: '#' + attribute.icon_color, borderColor: '#' + attribute.icon_color }"
											>{{ attribute.icon_name }}</li>
										</ul>
										<p class="item_title">{{ food.name }}</p>
										<p class="item_desc">{{ food.description }}</p>
										<p class="item_rating">月售 {{ food.month_sales }} 份 好评率 {{ food.satisfy_rate }} %</p>
										<!-- 活动标记 -->
										<div 
											v-if="food.activity" 
											class="activities" 
											:style="{ color: '#' + food.activity.image_text_color, borderColor: '#' + food.activity.icon_color }"
										>{{ food.activity.image_text }}</div>
									</div>
									<div class="edit_shop">
										<span class="price">
											￥<span class="price_num">{{ food.specfoods[0].price }}</span>
											<span v-if="food.specfoods.length > 1" class="price_start">起</span>
										</span>
										<!-- 编辑商品控件 -->
										<buy-cart 
											:food="food"
											@showReduceTip="showReduceTip"
											@showSpecsList="showSpecsList"
											@showMoveDot="handleShowMoveDot"
										></buy-cart>
									</div>
								</div>
							</router-link>
						</ul>
					</li>
				</ul>
			</div>
		</div>
		<!-- footer -->
		<div class="foot_car">
			<div class="cart_left" @click="showShopCart">
				<div class="cart_cycle" :class="{ active: selTotalNums > 0, move_in_cart: dotEnterState }" ref="cartCycle">
					<svg class="cart_icon">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-icon"></use>
                    </svg>
                    <span v-show="selTotalNums" class="num_stai">{{ selTotalNums }}</span>
				</div>
				<div class="cart_mes">
					<p class="sel_price">￥ {{ selTotalPrice | fixedTwo }}</p>
					<p class="delivery_fee">配送费￥{{ shopDetailsData.float_delivery_fee }}</p>
				</div>
			</div>
			<div class="cart_right" :class="{ active: showSettlement }">
				<span v-if="showSettlement" class="text">去结算</span>
				<span v-else class="text">还差￥{{ diffPriceToSend }}起送</span>
			</div>
		</div>
		<!-- 购物车列表 -->
		<transition name="cart">
			<div v-show="isShowCart" class="shop_cart_list">
				<header>
					<span class="title">购物车</span>
					<div class="clear_all" @click="clearShopCart">
						<svg class="clear_svg">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-remove"></use>
                        </svg>
						清空
					</div>
				</header>
				<ul class="shop_cart_ul">
					<li 
						class="shop_cart_item"
						v-for="item in shopCartFoodList"
						:key="'cart' + item.food_id"
					>
						<div class="item_left">
							<p class="name">{{ item.name }}</p>
							<p v-show="item.specs" class="spec">{{ item.specs }}</p>
						</div>
						<div class="item_price">
							￥<span class="price">{{ item.price }}</span>
						</div>
						<buy-cart :food="item"></buy-cart>
					</li>
				</ul>
			</div>
		</transition>
		<!-- 购物车列表遮罩层 -->
		<mask-layer v-if="isShowCart" @click.native="showShopCart"></mask-layer>
		<!-- 食品规格弹框 -->
		<transition name="fade">
			<div v-if="showSpecsLayer" class="specs_layer">
				<h4 class="specs_header">
					<span class="title">{{ choosedFood.name }}</span>
					<i class="iconfont close_icon" @click="showSpecsList(null)">&#xe617;</i>
				</h4>
				<div class="specs_list">
					<h5 class="specs_list_title">{{ choosedFood.specifications[0].name }}</h5>
					<ul class="specs_list_ul">
						<li 
							class="specs_list_item"
							:class="{ active: specsIndex == index }"
							v-for="(item, index) in choosedFood.specifications[0].values"
							:key="'specsIndex' + index"
							@click="toggleSpecsItem(index)"
						>
							{{ item }}
						</li>
					</ul>
				</div>
				<div class="specs_footer">
					<span class="specs_price">￥<strong>{{ choosedFood.specfoods[specsIndex].price }}</strong></span>
					<span class="add_btn" @click="addCartSpecs(choosedFood.category_id, choosedFood.item_id, choosedFood.specfoods[specsIndex].food_id, choosedFood.specfoods[specsIndex].name, choosedFood.specfoods[specsIndex].price, choosedFood.specfoods[specsIndex].specs_name, choosedFood.specfoods[specsIndex].packing_fee, choosedFood.specfoods[specsIndex].sku_id, choosedFood.specfoods[specsIndex].stock)"
					>加入购物车</span>
				</div>
			</div>
		</transition>
		<!-- 食品规格遮罩层 -->
		<mask-layer v-if="showSpecsLayer" @click.native="showSpecsList(null)"></mask-layer>
		<!-- 弹出规格删除提示 -->
		<transition name="fade">
			<div v-if="showReduceAlert" class="reduce_tip">多规格商品只能去购物车删除哦</div>
		</transition>
		<!-- 加入购物车的动画效果 -->
		<transition 
			appear
			@before-appear="dotBeforeEnter"
			@after-appear="dotAfterEnter"
			v-for="(item, index) in showMoveDotArr"
			:key="'dot' + index"
		>
			<span class="move_dot" v-if="item">
				<svg>
					<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-add"></use>
				</svg>
			</span>
		</transition>
	</div>
</template>

<script>
import MaskLayer from '@/components/common/MaskLayer'
import BuyCart from './BuyCart'
import BScroll from 'better-scroll'
import { getImgPath } from '@/mixins'
import { mapState, mapMutations } from 'vuex'
export default {
	name: 'ShopDetailList',
	components: {
		MaskLayer,
		BuyCart
	},
	props: {
		shopLeftMenu: Array,	// 列表所有数据
		shopDetailsData: Object
	},
	mixins: [getImgPath],
	data () {
		return {
			baseImgUrl: '//elm.cangdu.org/img/',
			selTotalNums: 0,	// 购物车内商品数量
			selTotalPrice: 0,	// 购物车内商品总价
			cateNumArr: [],		// 记录各商品分类的对应的数量
			showSpecsLayer: false,	// 控制显示食品规格弹框
			choosedFood: null,	// 记录下食品规格弹框对应的食品对象
			specsIndex: 0,		// 食品规格项的索引值
			showReduceAlert: false,  // 不能直接删除的提示
			showCart: false,		// 显示购物车
			shopCartFoodList: [],	// 购物车列表对应的食品数据(Array)
			showPointTipIndex: -1,	// 显示分类标题的描述提示 
			cateItemIndex: 0,		// 记录列表滚动的距离位置
			showMoveDotArr: [],		// 记录加入购物车动画的圆点数量
			dotLeft: null,			// 记录购物车动画圆点的初始位置(距离屏幕左侧)
			dotBottom: null,		// 记录购物车动画圆点的初始位置(距离屏幕顶部)
			dotEnterState: false 	// 监听购物车动画圆点是否落入购物车
		}
	},
	computed: {
		...mapState(['cartList', 'shopId']),
		diffPriceToSend () {
			// 差的金额
			let diffPrice = this.shopDetailsData.float_minimum_order_amount - this.selTotalPrice
			return diffPrice
		},
		showSettlement () {
			return this.diffPriceToSend <= 0
		},
		// 得到当前商铺购物车信息(返回一个新对象)
		getShopCart () {
			return {...this.cartList[this.shopId]}
		},
		// 是否显示购物车
		isShowCart () {
			return this.shopCartFoodList.length && this.showCart
		}
	},
	methods: {
		...mapMutations(['ADD_CART', 'CLEAR_CART']),
		// 初始化列表页面数据(初始化和shopCart变化时)
		initListData () {
			this.cateNumArr = []		// 重置各商品分类的对应的数量
			this.selTotalNums = 0		// 重置购物车的商品总数
			this.selTotalPrice = 0		// 重置购物车的商品总价
			this.shopCartFoodList = []	// 重置购物车列表里的食品数据
			this.shopLeftMenu.forEach((item, index) => {
				if (this.getShopCart[item.id]) {
					let cateNum = 0
					// 循环遍历每个分类id下面的item项
					Object.keys(this.getShopCart[item.id]).forEach((itemId) => {
						// 循环遍历每个item下面的food项
						Object.keys(this.getShopCart[item.id][itemId]).forEach((foodId) => {
							let foodObj = this.getShopCart[item.id][itemId][foodId]
							if (foodObj) {
								cateNum += foodObj.num
								// 更新总价
								if (item.type == 1) {
									this.selTotalPrice += foodObj.price * foodObj.num
								}
								// 向购物车列表添加食品项
								let shopCartFoodItem = {}
								shopCartFoodItem.category_id = item.id
								shopCartFoodItem.item_id = itemId
								shopCartFoodItem.food_id = foodId
								shopCartFoodItem.num = foodObj.num
								shopCartFoodItem.price = foodObj.price
								shopCartFoodItem.name = foodObj.name
								shopCartFoodItem.specs = foodObj.specs
								shopCartFoodItem.packing_fee = foodObj.packing_fee
								shopCartFoodItem.sku_id = foodObj.sku_id
								shopCartFoodItem.stock = foodObj.stock
								this.shopCartFoodList.push(shopCartFoodItem)
							}
						})
					})

					// 记录每个分类的商品数量
					this.cateNumArr[index] = cateNum
					// 更新购物车的商品总数
					this.selTotalNums += cateNum
				} else {
					this.cateNumArr[index] = 0
				}
			})
		},
		// 判断是否为新品
		isShopNew (attributes) {
			if (!attributes || attributes.length < 1) {
				return
			}
			let bol = false
			attributes.forEach((item) => {
				if (item.icon_name == '新') {
					bol = true
				}
			})
			return bol
		},
		// 点击多规格商品的减按钮，弹出提示
		showReduceTip () {
			if (this.showReduceAlert) {
				return
			}
			this.showReduceAlert = true
			setTimeout(() => {
				this.showReduceAlert = false
			}, 3000)
		},
		// 显示商品规格列表
		showSpecsList (foodObj) {
			// console.log(foodObj)
			// 重置规格index值
			this.specsIndex = 0
			// 
			if (foodObj) {
				this.choosedFood = foodObj
			}
			this.showSpecsLayer = !this.showSpecsLayer
		},
		// 切换商品规格
		toggleSpecsItem (index) {
			this.specsIndex = index
		},
		// 选择规格后加入购物车
		addCartSpecs (category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock) {
			let dataObj = {
				shopId: this.shopId,
				category_id, 
				item_id, 
				food_id, 
				name, 
				price, 
				specs, 
				packing_fee, 
				sku_id, 
				stock
			}
			this.ADD_CART(dataObj)
			// 关闭弹框
			this.showSpecsLayer = false
		},
		// 滑动出购物车
		showShopCart () {
			if (this.shopCartFoodList.length < 1) {
				return
			}
			this.showCart = !this.showCart
		},
		// 清空购物车
		clearShopCart () {
			this.CLEAR_CART(this.shopId)
		},
		// 显示分类标题的描述提示
		showPointTips (index) {
			if (this.showPointTipIndex === index) {
				this.showPointTipIndex = -1
				return
			}
			this.showPointTipIndex = index
		},
		// 点击左侧导航 右侧列表滚动到相应的位置
		scrollToPos (index) {
			let dom = document.querySelector('#cateItem' + index)
			this.rightScroll.scrollToElement(dom, 600)
			// this.cateItemIndex = index
		},
		// 监听加入购物车动画
		handleShowMoveDot (dotArr, dotLeft, dotBottom) {
			// console.log(dotArr)
			this.showMoveDotArr = [...this.showMoveDotArr, ...dotArr]
			// console.log(this.showMoveDotArr)
			this.dotLeft = dotLeft
			this.dotBottom = dotBottom
		},
		// 购物车图标加入之前
		dotBeforeEnter (el) {
			// console.log(this.showMoveDotArr)
			el.style.transform = `translate3d(0, ${this.dotBottom + 30 - window.innerHeight}px, 0)`
			el.children[0].style.transform = `translate3d(${this.dotLeft - 30}px, 0, 0)`
			el.children[0].style.opacity = 0
		},
		// 购物车图标加入之后
		dotAfterEnter (el) {
			// console.log('after')
			// 垂直方向运动
			el.style.transition = 'transform .55s cubic-bezier(0.3, -0.25, 0.7, -0.15)'
			el.style.transform = 'translate3d(0, 0, 0)'
			// 水平方向运动
			el.children[0].style.transition = 'transform .55s linear'
			el.children[0].style.transform = 'translate3d(0, 0, 0)'
			el.children[0].style.opacity = 1
			// 隐藏圆点
			this.showMoveDotArr = this.showMoveDotArr.map((item) => {
				return false
			})
			// 监听圆点动画过渡完成
			el.children[0].addEventListener('transitionend', this.watchDotEnter)
			// el.children[0].addEventListener('webkitAnimationEnd', this.watchDotEnter)
		},
		// 监听圆点已进入购物车
		watchDotEnter () {
			// console.log('dot enter')
			if (!this.dotEnterState) {
				// 添加类名执行动画
				this.dotEnterState = true
				// 动画完成后删除类名
				this.$refs.cartCycle.addEventListener('animationEnd', this.dotEnterFinish)
				this.$refs.cartCycle.addEventListener('webkitAnimationEnd', this.dotEnterFinish)
			}
		},
		dotEnterFinish () {
			// console.log('animation 完成')
			this.dotEnterState = false
			// 移除事件监听
			this.$refs.cartCycle.removeEventListener('webkitAnimationEnd', this.dotEnterFinish)
		}
	},
	watch: {
		// 监听购物车数据的变化(vuex)
		getShopCart () {
			this.initListData()
		},
		// 监听购物车列表的变化
		shopCartFoodList () {
			if (this.shopCartFoodList.length < 1) {
				this.showCart = false
			}
		}
	},
	filters: {
		fixedTwo (num) {
			return num.toFixed(2)
		}
	},
	mounted () {
		// 引入BScroll
		this.leftScroll = new BScroll(this.$refs.leftNav, {
			click: true
		})
		this.rightScroll = new BScroll(this.$refs.rightList, {
			click: true,
			probeType: 3	//探针, 获取滑动距离必备
		})
		// 监听右侧列表BScroll的滚动
		this.rightScroll.on('scroll', (pos) => {
			let index = 0
			let criHeight = 0
			while (index < this.shopLeftMenu.length) {
				// criHeight += document.querySelector('#cateItem' + index).style.height
				let dom = document.querySelector('#cateItem' + index)
				criHeight += parseInt(window.getComputedStyle(dom).height)
				// console.log(pos.y, -criHeight)
				if (pos.y > -criHeight) {
					this.cateItemIndex = index
					// 滚动到最底部时
					/*if (pos.y <= this.rightScroll.maxScrollY) {
						this.cateItemIndex++
					}*/
					return
				}
				index++
			}
		})
		//
		// console.log(this.cartList)
		this.initListData()
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	
	.border-bottom:before {
		border-color: $borderColor;
	}
	
	// fade
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to {
	  opacity: 0;
	}
	// cart
	.cart-enter-active, .cart-leave-active {
	  transition: transform .5s;
	  // transform: translateY(0);
	}
	.cart-enter, .cart-leave-to {
	  transform: translateY(100%);
	}
	// 圆点落入购物车后的动画
	@keyframes moveInCart {
		0% { transform: scale(1); }
		25% { transform: scale(.8); }
		50% { transform: scale(1.1); }
		75% { transform: scale(.9); }
		100% { transform: scale(1); }
	}

	.list_container {
		flex: 1;
		padding-bottom: .92rem;
		display: flex;
		.content {
			flex: 1;
			display: flex;
			.left_nav {
				width: 1.8rem;
				.nav_item {
					padding: .32rem .14rem;
					border-left: solid .06rem #f8f8f8;
					position: relative;
					line-height: .5rem;
					@include ellipsis;
					img {
						width: .24rem;
						height: .28rem;
					}
					.stati {
						position: absolute;
						top: .04rem;
						right: .04rem;
						width: .28rem;
						height: .28rem;
						line-height: .28rem;
						border-radius: 50%;
						background: #ff461d;
						color: #fff;
						text-align: center;
						font-size: .24rem;
					}
				}
				.nav_item.active {
					background: #fff;
					border-color: $blueColor;
					.name {
						font-weight: bold;
					}
				}
			}
			.right_list {
				flex: 1;
				.cate_item {
					.title {
						padding: .18rem;
						height: 1rem;
						display: flex;
						justify-content: space-between;
						align-items: center;
						.text {
							min-width: 0;
							flex: 1;
							padding-right: .4rem;
							.name {
								font-size: .32rem;
								font-weight: bold;
								margin-right: .1rem;
							}
							.desc {
								display: inline-block;
								font-size: .24rem;
								color: #999;
								width: 3.4rem;
								@include ellipsis;
							}
						}
						.point_icon {
							width: .24rem;
							height: .24rem;
							background: url('~@/images/icon_point.png') no-repeat center;
							background-size: .24rem auto;
							position: relative;
							.point_icon_tip {
								position: absolute;
								z-index: 5;
								top: .36rem;
								right: 0;
								padding: .24rem;
								width: 3.8rem;
								border-radius: .08rem;
								background-color: #39373a;
								opacity: .95;
								line-height: .4rem;
								color: #fff;
								.tip_name {
									font-size: .28rem;
									margin-right: .1rem;
								}
								.tip_desc {
									font-size: .24rem;
								}
							}
							.point_icon_tip:before {
								content: '';
								display: block;
								opacity: .95;
								position: absolute;
								top: -.12rem;
								right: .04rem;
								width: 0;
								height: 0;
								border: solid .18rem #39373a;
								border-top: none;
								border-left-color: transparent;
								border-right-color: transparent;
							}
						}
					}
					.item {
						background: #fff;
						padding: .28rem .18rem;
						display: flex;
						position: relative;
						overflow: hidden;
						.attribute_new {
							position: absolute;
							z-index: 2;
							left: 0;
							top: 0;
							width: .75rem;
							height: .75rem;
							background: #4cd964;
							display: flex;
							align-items: flex-end;
							justify-content: center;
							transform: rotate(-45deg) translate(0px, -.52rem);
							span {
								color: #fff;
								font-size: .24rem;
							}
						}
						.left_pic {
							width: .94rem;
							height: .94rem;
							margin-right: .2rem;
							overflow: hidden;
							position: relative;
							img {
								width: 100%;
								min-height: 100%;
							}
						}
						.right_con {
							min-width: 0;
							flex: 1;
							word-break: break-all;
							.desc {
								position: relative;
								.attributes_ul {
									position: absolute;
									top: 0;
									right: .1rem;
									display: flex;
									.attribute_item {
										line-height: .24rem;
										border: solid .02rem #eee;
										border-radius: .14rem;
										padding: 0 .04rem;
										text-align: center;
										font-size: .24rem;
										margin-left: .06rem;
									}
								}
								
								p {
									line-height: .4rem;
								}
								.item_title {
									color: #333;
									font-weight: bold;
									font-size: .32rem;
								}
								.item_desc {
									font-size: .24rem;
									color: #999;
									/* @include ellipsis; */
								}
								.item_rating {
									font-size: .24rem;
									color: #333;
								}
								.activities {
									display: inline-block;
									line-height: .28rem;
									border-radius: .16rem;
									border: solid .02rem #eee;
									text-align: center;
									padding: 0 .04rem;
									font-size: .24rem;
								}
							}
							.edit_shop {
								margin-top: .1rem;
								line-height: .4rem;
								display: flex;
								justify-content: space-between;
								align-items: center;
								.price {
									color: #f60;
									.price_num {
										font-weight: bold;
										font-size: .32rem;
									}
									.price_start {
										color: #666;
										font-size: .24rem;
									}
								}
							}
						}
					}
				}
			}
		}
		.foot_car {
			position: absolute;
			z-index: 120;
			left: 0;
			bottom: 0;
			width: 100%;
			height: .92rem;
			background: #3d3d3f;
			display: flex;
			.cart_left {
				flex: 1;
				position: relative;
				padding-left: 1.6rem;
				.cart_cycle {
					width: 1.1rem;
					height: 1.1rem;
					background: #3d3d3f;
					border: solid .08rem #444;
					border-radius: 50%; 
					position: absolute;
					left: .24rem;
					bottom: .2rem;
					display: flex;
					justify-content: center;
					align-items: center;
					.cart_icon {
						width: .56rem;
						height: .56rem;
					}
					.num_stai {
						position: absolute;
					    top: -.12rem;
					    right: 0;
					    width: .32rem;
					    height: .32rem;
					    line-height: .28rem;
					    border-radius: 50%;
					    background: #ff461d;
					    color: #fff;
					    text-align: center;
					    font-size: .24rem;
					}
				}
				.cart_cycle.active {
					background: $blueColor;
				}
				.cart_cycle.move_in_cart {
					animation: moveInCart .5s ease-in-out;
				}
				.cart_mes {
					.sel_price {
						font-size: .36rem;
						color: #fff;
						font-weight: 800;
						line-height: .56rem;
					}
					.delivery_fee {
						font-size: .24rem;
						color: #fff;
					}
				}
			}
			.cart_right {
				background: #535356;
				width: 2.4rem;
				line-height: .92rem;
				text-align: center;
				.text {
					color: #fff;
					font-size: .32rem;
					font-weight: 700;
				}
			}
			.cart_right.active {
				background: #4cd964;
			}
		}
		.shop_cart_list {
			position: fixed;
			z-index: 100;
			left: 0;
			bottom: 0;
			width: 100%;
			padding-bottom: .92rem;
			header {
				height: .9rem;
				line-height: .9rem;
				background: #eceff1;
				padding: 0 .28rem;
				display: flex;
				justify-content: space-between;
				.title {
					font-size: .32rem;
				}
				.clear_all {
					display: flex;
					align-items: center;
					.clear_svg {
						width: .28rem;
						height: .28rem;
						margin-right: .1rem;
					}
				}
			}
			.shop_cart_ul {
				max-height: 9.6rem;
				overflow-y: auto;
			}
			.shop_cart_item {
				padding: .28rem .24rem;
				background: #fff;
				display: flex;
				justify-content: space-between;
				align-items: center;
				.item_left {
					width: 55%;
					.name {
						font-size: .32rem;
						font-weight: bold;
						line-height: .4rem;
					}
					.spec {
						font-size: .24rem;
					}
				}
				.item_price {
					color: #f60;
					.price {
						font-size: .32rem;
						font-weight: bold;
					}
				}
			}
		}
		.specs_layer {
			width: 70%;
			position: fixed;
			z-index: 100;
			top: 50%;
			left: 15%;
			transform: translateY(-50%);
			background: #fff;
			border-radius: .1rem;
			.specs_header {
				height: .88rem;
				text-align: center;
				position: relative;
				.title {
					display: block;
					width: 70%;
					margin: 0 auto;
					@include ellipsis;
					line-height: .88rem;
					font-size: .32rem;
					color: #222
				}
				.close_icon {
					color: #222;
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					right: .3rem;
				}
			}
			.specs_list {
				padding: .24rem;
				.specs_list_title {
					line-height: .4rem;
					margin-bottom: .2rem;
				}
				.specs_list_ul {
					display: flex;
					flex-wrap: wrap;
					margin-bottom: .2rem;
					.specs_list_item {
						line-height: .36rem;
						border: solid .02rem #ddd;
						border-radius: .08rem;
						padding: .14rem .24rem;
						text-align: center;
						color: #333;
						margin-right: .24rem;
						margin-bottom: .1rem;
						word-break: break-all;
					}
					.specs_list_item.active {
						color: $blueColor;
						border-color: $blueColor;
					}
				}
			}
			.specs_footer {
				padding: .24rem;
				display: flex;
				justify-content: space-between;
				.specs_price {
					color: #ff6000;
					line-height: .6rem;
					strong {
						font-size: .32rem;
					}
				}
				.add_btn {
					height: .6rem;
					line-height: .6rem;
					padding: 0 .24rem;
					border-radius: .06rem;
					background: $blueColor;
					text-align: center;
					color: #fff;
				}
			}
		}
		.reduce_tip {
			position: fixed;
			z-index: 20;
			width: 70%;
			height: .86rem;
			line-height: .86rem;
			top: 50%;
			transform: translateY(-50%);
			left: 15%;
			background: rgba(0, 0, 0, .8);
			border-radius: .08rem;
			color: #fff;
			text-align: center;
			font-size: .32rem;
		}
		.move_dot {
			position: fixed;
			left: 30px;
			bottom: 30px;
			svg {
				display: inline-block;
				vertical-align: middle;
				width: .4rem;
				height: .4rem;
				fill: #3190e8;
			}
		}
	}
</style>


