<template>
	<div class="rating_star">
		<!-- bg -->
		<div class="star_box">
			<svg class="gray_fill" v-for="item in 5">
				<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#star"></use>
			</svg>
		</div>
		<!-- cover -->
		<div class="cover_stars" :style="{ width: (score / 5) * 100 + '%' }">
			<div class="star_box">
				<svg class="active_fill" v-for="item in 5">
					<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#star"></use>
				</svg>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'RatingStar',
	props: {
		score: [Number, String]
	},
	data () {
		return {

		}
	}
}
</script>

<style lang="scss" scoped>
	
	.rating_star {
		position: relative;
		.star_box {
			white-space: nowrap;
			svg {
				width: .18rem;
				height: .18rem;
			}
			.gray_fill {
				fill: #d1d1d1;
			}
			.active_fill {
				fill: #f60;
			}
		}
		.cover_stars {
			position: absolute;
			left: 0;
			top: 0;
			overflow: hidden;
		}
	}
</style>


