<template>
	<div class="shop-list">
		<ul>
			<router-link 
				tag="li"
				:to="{ path: 'shop', query: { geohash, id: item.id } }"
				class="item border-bottom"
				v-for="item in shopList"
				:key="item.id"
			>
				<div class="pic">
					<img :src="imgBaseUrl + item.image_path" alt="">
				</div>
				<div class="desc">
					<div class="top">
						<span class="brand" v-if="item.is_premium">品牌</span>
						<span class="d_title">{{ item.name }}</span>
						<ul class="ticket">
							<li v-for="support in item.supports" :key="support.id">{{ support.icon_name }}</li>
						</ul>
					</div>
					<div class="mid">
						<div class="rating_order_left">
							<div class="rating">
								评分: {{ item.rating }}
							</div>
							<div class="order">月售{{ item.recent_order_num }}单</div>
						</div>
						<ul class="rating_order_right">
							<li v-if="item.delivery_mode" class="blue_bg">{{ item.delivery_mode.text }}</li>
							<li v-if="punctual(item.supports)">准时达</li>
						</ul>
					</div>
					<div class="bot">
						<div class="fee">
							¥{{ item.float_minimum_order_amount }}起送<span>/</span>{{ item.piecewise_agent_fee.tips }}
						</div>
						<div class="distance_time">
							<span class="distance">{{ item.distance }}</span>
							<span>/</span>
							<span class="time">{{ item.order_lead_time }}</span>
						</div>
					</div>
				</div>
			</router-link>
		</ul>
		<div v-show="!hasMoreData" class="tips">已无更多数据!</div>
	</div>
</template>

<script>
import { getShopList } from '@/ajaxApi/getData'
// import { loadMore } from '@/mixins'
import { mapState } from 'vuex'
export default {
	name: 'ShopList',
	components: {
		
	},
	props: {
		geohash: String,
		restaurantCategoryId: [String, Number],
		restaurantCategoryIds: [String, Number],
		sortByType: [String, Number],
		supportWays: Object,
		hasToBot: Boolean
	},
	// mixins: [loadMore],
	data () {
		return {
			shopList: [],
			imgBaseUrl: '//elm.cangdu.org/img/',
			hasMoreData: true,
			offset: 0,	// 根据索引值批次加载店铺列表, 每次限制只加载20个 limit = 20
			limitRequestFre: false	// 限制请求频率
		}
	},
	computed: {
		...mapState(['latitude', 'longitude'])
	},
	methods: {
		// 成功加载列表数据的回调
		getShopListSucc (res) {
			res = res.data
			// console.log(res)
			if (res.message) {
				console.error('获取数据失败: ' + res.message)
				return
			}
			this.shopList = res
			// 判断是否有更多的数据
			if (res.length < 20) {
				this.hasMoreData = false
			}
			// 关闭加载等待动画
			this.$emit('changeLoading', false)
			// 开始监听列表滚动的距离, 达到一定程度后显示返回顶部按钮

		},
		// 判断是否有准时送达的支持
		punctual (supports) {
			let state = false
			if (supports instanceof Array && supports.length) {
				supports.forEach((item) => {
					if (item.icon_name === '准') {
						state = true
					}
				})
			}
			return state
		},
		// 下拉刷新加载更多数据
		loadMoreData () {
			// 没有更多数据
			if (!this.hasMoreData) {
				return
			}
			// 更新索引值()
			this.offset += 20
			// 显示加载动画
			this.$emit('changeLoading', true)
			// 请求更多数据
			this.limitRequestFre = true		// 开启请求限制
			// 判断筛选方式是否为null
			let deliveryMode, activityList
			if (this.supportWays) {
				deliveryMode = this.supportWays.deliveryMode
				activityList = this.supportWays.activityList
			} else {
				deliveryMode = ''
				activityList = []
			}
			// 判断是否为分类查询
			let categoryId
			if (this.restaurantCategoryIds) {
				categoryId = ''
			} else {
				categoryId = this.restaurantCategoryId
			}
			// ajax
			getShopList(this.latitude, this.longitude, this.offset, categoryId, this.restaurantCategoryIds, this.sortByType, deliveryMode, activityList)
				.then(this.loadMoreDataSucc)
		},
		loadMoreDataSucc (res) {
			res = res.data
			console.log(res)
			
			// 判断是否有更多的数据
			if (res.length < 20) {
				this.hasMoreData = false
			}
			// 合并之前的数据
			this.shopList = this.shopList.concat(res)
			// 合并数据后需重新刷新父组件的BScroll
			this.$nextTick(() => {
				// DOM更新后
				this.$emit('refresh')
				this.limitRequestFre = false	// 关闭请求限制
				// 关闭加载等待动画
				this.$emit('changeLoading', false)
			})
		},
		// 按条件重新筛选数据
		loadDataByQuery () {
			this.hasMoreData = true
			// 显示加载动画
			this.$emit('changeLoading', true)
			// 从第一条数据开始查询 
			this.offset = 0
			// 判断筛选方式是否为null
			let deliveryMode, activityList
			if (this.supportWays) {
				deliveryMode = this.supportWays.deliveryMode
				activityList = this.supportWays.activityList
			} else {
				deliveryMode = ''
				activityList = []
			}
			// 判断是否为分类查询
			let categoryId
			if (this.restaurantCategoryIds) {
				categoryId = ''
			} else {
				categoryId = this.restaurantCategoryId
			}
			// ajax
			getShopList(this.latitude, this.longitude, this.offset, categoryId, this.restaurantCategoryIds, this.sortByType, deliveryMode, activityList)
				.then((res) => {
					res = res.data
					// console.log(res)
					this.shopList = res
					// 关闭加载等待动画
					this.$emit('changeLoading', false)
					// 判断是否有更多的数据
					if (res.length < 20) {
						this.hasMoreData = false
					}
				})
		}
	},
	watch: {
		// 监听父组件传递过来的是否滚动到底部的布尔值的变化
		hasToBot () {
			if (this.hasToBot) {
				// console.log('滚动到底部: ' + this.hasToBot)
				if (this.limitRequestFre) {
					return
				}
				this.loadMoreData()
			}
		},
		// 监听父组件传递过来的筛选条件参数的变化
		restaurantCategoryIds () {
			// console.log('重新筛选数据')
			this.loadDataByQuery()
		},
		sortByType () {
			this.loadDataByQuery()
		},
		supportWays () {
			// console.log(this.supportWays)
			this.loadDataByQuery()
		}
	},
	mounted () {
		// console.log(this.restaurantCategoryId)
		// 加载列表数据(初始化)
		getShopList(this.latitude, this.longitude, this.offset, this.restaurantCategoryId).then(this.getShopListSucc)
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	
	.border-bottom:before {
		border-color: $borderColor;
	}

	.shop-list {
		background: #fff;
		.item {
			padding: .34rem .16rem;
			display: flex;
			.pic {
				width: 1.28rem;
				height: 1.28rem;
				margin-right: .16rem;
				overflow: hidden;
				background: #eee;
				img {
					width: 100%;
					min-height: 100%;
				}
			}
			.desc {
				flex: 1;
				.top {
					margin-bottom: .2rem;
					.brand, .d_title {
						display: inline-block;
						vertical-align: middle;
						color: #333;
						font-weight: bold;
					}
					.brand {
						background: #ffd930;
						padding: 0 .04rem;
						border-radius: .04rem;
						height: .28rem;
						line-height: .28rem;
						font-size: .24rem;
						margin-right: .08rem;
					}
					.d_title {
						font-size: .32rem;
						max-width: 3.6rem;
						@include ellipsis;
					}
					.ticket {
						float: right;
						display: flex;
						transform: scale(.8);
						li {
							font-size: .24rem;
							color: #999;
							border: solid .02rem #f1f1f1;
							padding: 0 .02rem;
							border-radius: .04rem;
							margin: 0 .02rem;
						}
					}
				}
				.mid {
					display: flex;
					justify-content: space-between;
					font-size: .24rem;
					margin-bottom: .2rem;
					.rating_order_left {
						display: flex;
						.rating {
							color: #ff6000;
							margin-right: .1rem;
						}
						.order {
							
						}
					}
					.rating_order_right {
						display: flex;
						justify-content: flex-end;
						transform: scale(.7);
						min-width: 2.4rem;
						margin-right: -.2rem;
						li {
							background: #fff;
							border: solid .02rem $blueColor;
							border-radius: .04rem;
							color: $blueColor;
							padding: .02rem .04rem;
							margin: 0 .02rem;
						}
						li.blue_bg {
							background: $blueColor;
							color: #fff;
						}
					}
				}
				.bot {
					display: flex;
					justify-content: space-between;
					font-size: .24rem;
					.fee {
						color: #666;
					}
					.distance_time {
						transform: scale(.9);
    					margin-right: 5px;
						.distance {
							color: #999;
						}
						.time {
							color: #3190e8;
						}
					}
				}
			}
		}
		.tips {
			line-height: .6rem;
			text-align: center;
			color: #333;
		}
	}

</style>


