import Vue from 'vue'
import Router from 'vue-router'
import Home from '../page/home/Home'
import Login from '../page/login/Login'
import Forget from '../page/login/Forget'
import Profile from '../page/profile/Profile'
import ProfileInfo from '../page/profile/components/ProfileInfo'
import City from '../page/city/City'
import Msite from '../page/msite/Msite'
import Food from '../page/food/Food'
import Search from '../page/search/Search'
import Shop from '../page/shop/Shop'
import FoodDetail from '../page/shop/components/FoodDetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
      	{
      		path: 'home',
      		redirect: '/',
      	}	
      ]
    },
    // 用户登录
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/forget',
      name: 'Forget',
      component: Forget
    },
    // 用户-我的
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
      children: [
        {
          path: 'info',
          name: 'profileInfo',
          component: ProfileInfo
        }
      ]
    },
    {
      path: '/city/:id',
      name: 'City',
      component: City
    },
    {
      path: '/msite',
      name: 'Msite',
      component: Msite
    },
    {
      path: '/food',
      name: 'Food',
      component: Food
    },
    {
      path: '/search/:geohash',
      name: 'Search',
      component: Search
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop,
      children: [
        {
          path: 'foodDetail',
          name: 'FoodDetail',
          component: FoodDetail
        }
      ]
    }
  ]
})



