const RECORD_USERINFO = 'RECORD_USERINFO'
const GET_USERINFO = 'GET_USERINFO'
const SAVE_GEOHASH = 'SAVE_GEOHASH'
const RECORD_ADDRESS = 'RECORD_ADDRESS'
const SAVE_SHOPID = 'SAVE_SHOPID'
const ADD_CART = 'ADD_CART'
const REDUCE_CART = 'REDUCE_CART'
const CLEAR_CART = 'CLEAR_CART'

import { setStore } from '@/config/utils'
export default {
	// 记录用户信息
	[RECORD_USERINFO] (state, userInfo) {
		state.userInfo = userInfo
		this.login = true
		// 把user_id储存到localStorage中
		setStore('user_id', userInfo.user_id)
	},
	// 储存用户信息
	[GET_USERINFO] (state, info) {
		state.userInfo = {...info}
	},
	// 保存geohash
	[SAVE_GEOHASH] (state, geohash) {
		state.geohash = geohash
	},
	// 记录当前经度纬度
	[RECORD_ADDRESS] (state, { latitude, longitude }) {
		state.latitude = latitude
		state.longitude = longitude
	},
	// 储存商铺id
	[SAVE_SHOPID] (state, id) {
		state.shopId = id
	},
	// 加入购物车
	[ADD_CART] (state, res) {
		// console.log(res)
		let cartObj = state.cartList
		let shop = cartObj[res.shopId] = (cartObj[res.shopId] || {})
		let category = shop[res.category_id] = (shop[res.category_id] || {})
		let item = category[res.item_id] = (category[res.item_id] || {})
		if (item[res.food_id]) {
			item[res.food_id].num++
		} else {
			item[res.food_id] = {
				'num': 1,
				'id': res.food_id,
				'name': res.name,
				'price': res.price,
				'specs': res.specs,
				'packing_fee': res.packing_fee,
				'sku_id': res.sku_id,
				'stock': res.stock
			}
		}
		/*console.log(state.cartList)
		console.log(cartObj)
		console.log(state.cartList === cartObj)*/
		// 改变state.cartList的引用
		state.cartList = {...cartObj}
	},
	// 移出购物车
	[REDUCE_CART] (state, res) {
		// console.log(res)
		let cartList = state.cartList
		let food = cartList[res.shopId][res.category_id][res.item_id][res.food_id]
		console.log(food)
		food.num--
		if (food.num < 1) {
			food = cartList[res.shopId][res.category_id][res.item_id][res.food_id] = null
		}
		// 改变state.cartList的引用
		state.cartList = {...cartList}
	},
	// 清空指定商铺的购物车
	[CLEAR_CART] (state, shopId) {
		let cartList = state.cartList
		cartList[shopId] = null
		// 改变state.cartList的引用
		state.cartList = {...cartList}
	}
}


