<template>
	<div class="list">
		<div class="result">
			<ul>
				<li class="item border-bottom" v-for="(item, index) in list" :key="'item' + index" @click="nextPage(index, item.geohash)">
					<h4 class="name">{{ item.name }}</h4>
					<p class="address">{{ item.address }}</p>
				</li>
			</ul>
			<div class="tip" v-show="showTips">很抱歉! 无搜索结果</div>
		</div>
		<div class="history" v-show="showHistory">
			<h4 class="title">搜索历史</h4>
			<ul>
				<li class="item border-bottom" v-for="(item, index) in historyListTemp" :key="'itemh' + index" @click="nextPage(index, item.geohash)">
					<h4 class="name">{{ item.name }}</h4>
					<p class="address">{{ item.address }}</p>
				</li>
			</ul>
			<div v-if="showClearAll" class="clear_all" @click="clearAll">清空所有</div>
		</div>
	</div>
</template>

<script>
import { setStore, getStore, removeStore } from '@/config/utils'
export default {
	name: 'CitySearchList',
	props: {
		list: Array,
		historyList: Array,
		showHistory: Boolean
	},
	data () {
		return {
			historyListTemp: this.historyList
		}
	},
	computed: {
		showTips () {
			return !this.showHistory && !this.list.length
		},
		showClearAll () {
			return this.showHistory && this.historyListTemp.length
		}
	},
	methods: {
		// 跳转页面之前先通过localStorage存储记录
		nextPage (index, geohash) {
			let history = getStore('historyList')
			if (!history) {	// 第一次存储
				let storeArr = []
				storeArr.push(this.list[index])
				setStore('historyList', storeArr)
			} else {
				// 先判断是否为重复项
				let isRepeat = false
				this.historyListTemp.forEach((item) => {
					if (item.geohash === geohash) {
						isRepeat = true
					}
				})
				if (!isRepeat) {
					this.historyListTemp.push(this.list[index])
					setStore('historyList', this.historyListTemp)
				}
			}

			this.$router.push({ path: '/msite', query: { geohash } })
		},
		// 清空所有历史记录
		clearAll () {
			this.historyListTemp = []
			removeStore('historyList')
		}
	},
	mounted () {
		console.log('开始: ' + this.historyList)
	}
}
</script>

<style lang="scss" scoped>
	@import '~style/mixin';
	
	.border-bottom {
		&:before {
			border-color: $borderColor;
		}
	}
	
	.list {
		background: #fff;
		.item {
			padding: .3rem 0;
			.name, .address {
				width: 90%;
				margin: 0 auto;
				line-height: .4rem;
				@include ellipsis;
			}
			.name {
				margin-bottom: .2rem;
				font-size: .32rem;
				color: #333;
			}
			.address {
				font-size: .24rem;
				color: #999;
			}
		}
		.result {	
			.tip {
				height: .8rem;
				line-height: .8rem;
				padding-left: .2rem;
				background: #fff;
				color: #333;
			}
		}
		.history {
			.title {
				border-bottom: solid 2px $borderColor;
				padding-left: .2rem;
				line-height: .4rem;
				font-size: .24rem;
			}
			.clear_all {
				height: .92rem;
				line-height: .92rem;
				text-align: center;
				font-size: .32rem;
			}
		}
	}
	
</style>


