<template>
	<div>
		<h3 class="title">搜索历史</h3>
		<div class="list_wrap">
			<ul>
				<li 
					class="item border-bottom" 
					v-for="(item, index) in listTemp" 
					:key="index"
					@click="handleClickSearch(item)"
				>
					<span class="text">{{ item }}</span>
					<i class="iconfont close_icon" @click.stop="removeItem(item)">&#xe617;</i>
				</li>
			</ul>
			<div class="clear" @click="removeAll">清空搜索历史</div>
		</div>
	</div>
</template>

<script>
import { removeStore, setStore, getStore } from '@/config/utils'
export default {
	name: 'SearchHistory',
	props: {
		list: Array
	},
	data () {
		return {
			listTemp: this.list
		}
	},
	computed: {
		showClear () {
			return this.list.length
		}
	},
	methods: {
		handleClickSearch (keyword) {
			this.$emit('search', keyword)
		},
		removeItem (name) {
			let history = getStore('searchHistory')
			history = JSON.parse(history)
			let newHistory = history.filter((item) => {
				return item !== name
			})
			// console.log(newHistory)
			this.listTemp = newHistory
			setStore('searchHistory', newHistory)	// 重置localStorage
			if (this.listTemp.length < 1) {
				removeStore('searchHistory')	// 删除所有
				this.$emit('hide')		// 隐藏历史列表
			}
		},
		removeAll () {
			this.listTemp = []
			removeStore('searchHistory')	// 删除所有
			this.$emit('hide')		// 隐藏历史列表
		}
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	.border-bottom:before {
		border-color: $borderColor;
	}

	.title {
		height: .92rem;
		line-height: .92rem;
		padding-left: .24rem;
	}
	.list_wrap {
		background: #fff;
		.item {
			line-height: .92rem;
			padding: 0 .16rem;
			display: flex;
			justify-content: space-between;
			.text {
				color: #333;
				font-size: .32rem;
			}
			.close_icon {
				color: #666;
				font-size: .24rem;
				min-width: .6rem;
				text-align: center;
			}
		}
	}
	.clear {
		height: .92rem;
		line-height: .92rem;
		text-align: center;
		font-size: .32rem;
		color: $blueColor;
		font-weight: bold;
	}
</style>


