/* vuex -- actions */
import { getUser } from '@/ajaxApi/getData'

export default {
	// 获取登录信息
	async getUserInfo ({ commit, state }) {
		if (state.login) {	// 已登陆时不用再去重复获取
			return
		}
		let res = await getUser()
		res = res.data
		console.log(res)
		// 把获取到的登录信息储存到vuex中
		if (res.user_id) {	// 登录成功
			state.login = true
			commit('GET_USERINFO', res)
		}
	}	
}


