<template>
	<div class="header">
		<slot name="logo"></slot>
		<i class="iconfont back_icon" v-if="showBack" @click="$router.go(-1)">&#xe624;</i>
		<div class="title">{{ headTitle }}</div>
		<slot name="headTitle"></slot>
		<router-link v-if="showLogin" tag="div" :to="userInfo ? '/profile' : '/login'" class="login">
			<svg v-if="userInfo" class="user_icon">
				<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#user"></use>
			</svg>
			<span v-else>登录|注册</span>
		</router-link>
		<slot name="changeCity"></slot>
	</div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
	name: 'HeaderTop',
	props: {
		showLogin: [Boolean, String],
		headTitle: [String, Number],
		showBack: String
	},
	data () {
		return {

		}
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		...mapActions(['getUserInfo'])
	},
	mounted () {
		console.log(this.userInfo);
		// 获取用户登录信息
		this.getUserInfo()
	} 
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	
	.header {
		position: fixed;
		left: 0;
		top: 0;
		z-index: 99;
		width: 100%;
		height: .92rem;
		background-color: $blueColor;
		padding: 0 .2rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.login, .back_icon {
			font-size: .32rem;
			color: #fff;
		}
		.login {
			.user_icon {
				width: .38rem;
				height: .38rem;
				fill: #fff;
			}
		}
		.title {
			@include headerTitleCentered;
		}
	}

</style>


