<template>
	<div class="food_detail">
		<header-top :headTitle="foodName" showBack="true"></header-top>
		<div class="food_img_wrap">
			<img :src="baseImgUrl + imagePath" alt="">
		</div>
		<div class="food_text_content">
			<p v-show="description" class="desc">{{ description }}</p>
			<p class="food_name">{{ foodName }}</p>
			<div class="rating_wrap">
				<span class="rating_text">评分:</span>
				<rating-star :score="rating"></rating-star>
				<span class="rating_score">{{ rating }}</span>
			</div>
			<p>
				<span class="mar_r">月售 {{ monthSales }}单</span>
				<span class="stress">售价 ￥{{ foodObj.specfoods[0].price }} 起</span>
			</p>
			<p>
				<span class="mar_r">评论数 {{ ratingCount }}</span>
				<span>好评率 {{ satisfyRate }}%</span>
			</p>
		</div>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
import RatingStar from '@/components/common/RatingStar'
export default {
	name: 'FoodDetail',
	components: {
		HeaderTop,
		RatingStar
	},
	data () {
		return {
			baseImgUrl: '//elm.cangdu.org/img/',
			imagePath: '',
			foodName: '',
			description: '',
			rating: '',
			monthSales: '',
			ratingCount: '',
			satisfyRate: ''
		}
	},
	computed: {

	},
	methods: {

	},
	created () {
		this.imagePath = this.$route.query.image_path
		this.foodName = this.$route.query.name
		this.description = this.$route.query.description
		this.rating = this.$route.query.rating
		this.monthSales = this.$route.query.month_sales
		this.ratingCount = this.$route.query.rating_count
		this.satisfyRate = this.$route.query.satisfy_rate
		this.foodObj = this.$route.query.food
	},
	mounted () {
		// console.log(this.$route.query.food)
	}
}	
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	
	.food_detail {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: 20;
		background: #fff;
		padding-top: .92rem;
		.food_img_wrap {
			width: 100%;
			height: 0;
			padding-bottom: 100%;
			img {
				width: 100%;
			}
		}
		.food_text_content {
			padding: .24rem;
			p {
				line-height: .4rem;
				margin-bottom: .1rem;
			}
			.desc {
				line-height: .36rem;
				margin-bottom: .24rem;
			}
			.food_name {
				color: #333;
				font-size: .32rem;
				height: .4rem;
				@include ellipsis;
			}
			.rating_wrap {
				line-height: .4rem;
				margin-bottom: .1rem;
				display: flex;
				.rating_text {
					margin-right: .1rem;
				}
				.rating_score {
					font-size: .24rem;
					color: #f60;
					margin-left: .1rem;
				}
			}
		}
		.stress {
			color: #f60;
		}
		.mar_r {
			margin-right: .2rem;
		}
	}
</style>


