// ajax接口调用
import axios from 'axios'
import { getStore } from '@/config/utils'
/**
 * 获取当前城市
 */
export const getCurrentCities = () => {
 	return axios.get(BASE_URL + '/v1/cities', {
 		params: { type: 'guess' }
 	})
}

/**
 * 获取首页热门城市
 */
export const getHotCities = () => {
 	return axios.get(BASE_URL + '/v1/cities', {
 		params: { type: 'hot' }
 	})
}

/**
 * 获取所有城市
 */
export const getAllCities = () => {
 	return axios.get(BASE_URL + '/v1/cities', {
 		params: { type: 'group' }
 	})
}

/**
 * 根据id获取当前所在城市
 * params {String} id 城市id
 */
export const getCityById = (id) => {
	return axios.get(BASE_URL + '/v1/cities/' + id)
}

/**
* 根据关键字获取搜索项
* params {String} id 城市id
*/
export const getCitySearchList = (id, keyWord) => {
	return axios.get(BASE_URL + '/v1/pois', {
		params: {
			type: 'search',
			city_id: id,
			keyword: keyWord
		}
	})
}

/**
 * 获取msite页面地址信息(纬度 经度)
 */
export const getMsiteAddress = (geohash) => {
	return axios.get(BASE_URL + '/v2/pois/' + geohash)
}

/**
 * 获取msite页面食品分类列表
 */
export const getMsiteFoodTypes = (geohash) => {
	return axios.get(BASE_URL + '/v2/index_entry', {
		params: {
			geohash,
			group_type: '1',
			'flags[]': 'F'
		}
	})
}

/**
 * 获取商铺列表信息
 */
export const getShopList = (latitude, longitude, offset, restaurant_category_id = '', restaurant_category_ids = '', order_by = '', delivery_mode = '', support_ids = []) => {
	let supportStrs = ''
	if (support_ids.length > 0) {
		support_ids.forEach((item, index) => {
			// console.log(index)
			if (index < 1) {
				supportStrs += item
			} else {
				supportStrs += ',' + item
			}
			
		})
	}
	let data = {
		latitude,
		longitude,
		offset,
		limit: 20,	// 默认首次请求20条数据
		'extras[]': 'activities',
		restaurant_category_id,
		'restaurant_category_ids[]': restaurant_category_ids,
		order_by,
		'delivery_mode[]': delivery_mode,
		'support_ids[]': supportStrs
	}
	return axios.get(BASE_URL + '/shopping/restaurants', {
		params: data
	})
}

/**
 * 获取商铺详情信息
 */
export const getShopDetails = (id, latitude, longitude) => {
	return axios.get(BASE_URL + '/shopping/restaurant/' + id, {
		params: {
			latitude,
			longitude,
			'extras[]': 'activities'
			// 'extras[]': 'album',
			// 'extras[]': 'license',
			// 'extras[]': 'identification',
			// 'extras[]': 'statistics'
		}
	})
}

/**
 * 获取商铺食品列表信息
 */
export const getFoodMenu = (id) => {
	return axios.get(BASE_URL + '/shopping/v2/menu', {
		params: {
			restaurant_id: id
		}
	})
}

/**
 * 获取food页面的 category 种类列表
 */
export const getFoodCategory = (latitude, longitude) => {
	return axios(BASE_URL + '/shopping/v2/restaurant/category', {
		params: {
			latitude,
			longitude
		}
	})
}

/**
 * 获取food页面的配送方式
 */
export const getFoodDelivery = (latitude, longitude) => {
	return axios(BASE_URL + '/shopping/v1/restaurants/delivery_modes', {
		params: {
			latitude,
			longitude,
			kw: ''
		}
	})
}

/**
 * 获取food页面的商家属性活动列表
 */
export const getFoodActivity = (latitude, longitude) => {
	return axios(BASE_URL + '/shopping/v1/restaurants/activity_attributes', {
		params: {
			latitude,
			longitude,
			kw: ''
		}
	})
}

/**
 * 获取search页面搜索结果
 */
export const getSearchList = (geohash, keyword) => {
	return axios.get(BASE_URL + '/v4/restaurants', {
		params: {
			'extras[]': 'restaurant_activity',
			type: 'search',
			geohash,
			keyword
		}
	})
}

/**
 * 获取验证吗，线上环境使用固定的图片，生产环境使用真实的验证码
 */
export const getVerifyCode = () => {
	return axios.post(BASE_URL + '/v1/captchas', {})
}

/**
 * 通过用户名密码登录(直接注册)
 */
export const accountLogin = (username, password, captcha_code) => {
	return axios.post(BASE_URL + '/v2/login', { username, password, captcha_code })
}

/**
 * 重置登录密码
 */
export const resetLoginPassword = (username, oldpassWord, newpassword, confirmpassword, captcha_code) => {
	return axios.post(BASE_URL + '/v2/changepassword', { username, oldpassWord, newpassword, confirmpassword, captcha_code })
}

/**
 * 通过储存在localStorage中的user_id去获取登录信息
 */
export const getUser = () => {
	return axios.get(BASE_URL + '/v1/user', {
		params: { user_id: getStore('user_id') }
	})
}


