<template>
	<div>
		<header-top showLogin="msite">
			<router-link tag="div" to="/" class="head_title" slot="headTitle">{{ headTitle }}</router-link>
			<router-link :to="`/search/${geohash}`" slot="logo">
				<i class="iconfont search_icon">&#xe632;</i>
			</router-link>
		</header-top>
		<div class="content" ref="content">
			<div>
				<!-- swiper -->
				<msite-swiper :geohash="geohash" :foodTypes="foodTypes"></msite-swiper>
				<!-- 列表 -->
				<div class="title">
					<svg class="shop_svg">
		    			<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#shop"></use>
		    		</svg>
					附件商家
				</div>
				<shop-list 
					v-if="showMsiteList" 
					:geohash="geohash" 
					:hasToBot="hasToBot" 
					@changeLoading="handleChangeLoading"
					@refresh="handleRefreshScroll"
				>
				</shop-list>
			</div>
		</div>
		<!-- loading动画 -->
		<Loading v-if="showLoading"></Loading>
		<!-- 列表(shop-list)返回顶部按钮 -->
		<show-back v-show="showBack" @back="handleBackTop"></show-back>
		<!-- foot -->
		<foot-guide></foot-guide>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import HeaderTop from '@/components/header/Header'
import MsiteSwiper from './components/MsiteSwiper'
import Loading from '@/components/common/Loading'
import ShopList from '@/components/common/ShopList'
import ShowBack from '@/components/common/ShowBack'
import FootGuide from '@/components/footer/FootGuide'
// import { showBackTop } from '@/config/utils'
import { getMsiteFoodTypes, getCurrentCities, getMsiteAddress } from '@/ajaxApi/getData'
import { mapMutations } from 'vuex'
export default {
	name: 'Msite',
	components: {
		HeaderTop,
		MsiteSwiper,
		ShopList,
		FootGuide,
		ShowBack,
		Loading
	},
	data () {
		return {
			headTitle: '请选择地址...',
			geohash: '',	// city页面传递过来的地址geohash
			foodTypes: [],
			msiteList: [],
			showLoading: true,	// 显示加载动画
			showMsiteList: false,
			showBack: false,	// 是否显示列表右下角返回顶部的按钮
			hasToBot: false		// 判断列表滚动到底部的状态值
		}
	},
	methods: {
		...mapMutations([
			'SAVE_GEOHASH', 'RECORD_ADDRESS'
		]),
		handleBackTop () {
			console.log(this.scroll)
			this.scroll.scrollTo(0, 0, 600)
		},
		handleChangeLoading (state) {
			this.showLoading = state
		},
		handleRefreshScroll () {
			this.scroll.refresh()
		}
	},
	async beforeMount () {
		// 获取当前地理位置
		this.geohash = this.$route.query.geohash
		if (!this.geohash) {
			let address = await getCurrentCities()
			address = address.data
			this.geohash = address.latitude + ',' + address.longitude
		}

		// console.log('save vuex')
		// 储存geohash
		this.SAVE_GEOHASH(this.geohash)
		// 获取当前位置
		let curAddress = await getMsiteAddress(this.geohash)
		curAddress = curAddress.data
		this.headTitle = curAddress.name
		// 记录当前经度纬度
		this.RECORD_ADDRESS(curAddress)

		this.showMsiteList = true
	},
	mounted () {
		// BScroll
		this.scroll = new BScroll(this.$refs.content, {
			click: true,
			probeType: 3,	//探针, 获取滑动距离必备
		})
		// 监听BScroll的滚动
		this.scroll.on('scroll', (pos) => {
			// console.log(this.scroll)
			if (pos.y <= -500) {
				this.showBack = true
			} else {
				this.showBack = false
			}

			// 监听是否滚动到底部
			if (pos.y < this.scroll.maxScrollY) {
				// console.log(this.hasToBot)
				this.hasToBot = true
			} else {
				this.hasToBot = false
			}
		})
		// 获取页面食品分类列表
		getMsiteFoodTypes(this.geohash).then((res) => {
			res = res.data
			this.foodTypes = res
		})

	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';

	.head_title {
		@include headerTitleCentered;
	}
	.search_icon {
		color: #fff;
		font-size: .4rem;
	}
	.content {
		@include scrollMidContent;
		.title {
			line-height: .72rem;
			padding-left: .3rem;
			color: #999;
			background: #fff;
			border-top: solid 1px $borderColor;
			.shop_svg {
				width: .28rem;
				height: .28rem;
				vertical-align: middle;
				fill: #999;
			}
		}
	}
</style>


