<template>
	<div class="msite_swiper">
		<swiper :options="swiperOption">
		    <!-- slides -->
		    <swiper-slide v-for="(item, index) in foodTypesFormat" :key="'slide' + index">
		    	<router-link 
		    		tag="div" 
		    		:to="{ path: '/food', query: { geohash, title: obj.title, restaurant_category_id: getCategoryId(obj.link) } }" 
		    		class="swiper_item" 
		    		v-for="obj in item" 
		    		:key="obj.id"
		    	>
		    		<div class="pic">
		    			<img :src="imgBaseUrl + obj.image_url" alt="">
		    		</div>
		    		<p class="desc">{{ obj.title }}</p>
		    	</router-link>
		    </swiper-slide>
		    <!-- Optional controls -->
		    <div class="swiper-pagination"  slot="pagination"></div>
		</swiper>
	</div>
</template>

<script>
export default {
	name: 'MsiteSwiper',
	props: {
		foodTypes: Array,
		geohash: String
	},
	data () {
		return {
			// swiper设置项
			swiperOption: {
				pagination: '.swiper-pagination'
			},
			imgBaseUrl: '//fuss10.elemecdn.com'
		}
	},
	computed: {
		foodTypesFormat () {
			let arr = []
			for (let i = 0; i < this.foodTypes.length; i++) {
				let index = Math.floor(i / 8)
				if (!arr[index]) {
					arr[index] = []
				}
				arr[index].push(this.foodTypes[i])
			}
			return arr
		}
	},
	methods: {
		// 解码url地址, 得到restaurant_category_id的值
		getCategoryId (url) {
			let urlData = decodeURIComponent(url.split('=')[1].replace('&target_name', ''))
			if (/restaurant_category_id/gi.test(urlData)) {
				return JSON.parse(urlData).restaurant_category_id.id
			} else {
				return ''
			}
		}
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	.msite_swiper {
		border-bottom: solid 1px $borderColor;
		background: #fff;
		margin-bottom: .2rem;
		.swiper-slide {
			display: flex;
			flex-wrap: wrap;
			height: 4rem;
			overflow: hidden;
			.swiper_item {
				width: 25%;
				height: 1.8rem;
				.pic {
					width: .84rem;
					height: .84rem;
					margin: .2rem auto;
					overflow: hidden;
					img {
						width: 100%;
					}
				}
				.desc {
					color: #666;
					line-height: 1;
					padding: 0 .1rem;
					text-align: center;
					@include ellipsis;
				}
			}
		}
	}
</style>


