<template>
	<div>
		<header-top showBack="true" :headTitle="headTitle"></header-top>
		<div class="content" ref="content">
			<div>
				<search-form @history="showHistoryList" @search="handleSearch" :inputVal="inputVal"></search-form>
				<search-history v-if="showSearchHistory" :list="historyList" @search="handleSearch" @hide="hideHistoryList"></search-history>
				<search-list v-if="showSearchList" :list="searchList"></search-list>
			</div>
		</div>
		<!-- foot -->
		<foot-guide></foot-guide>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
import FootGuide from '@/components/footer/FootGuide'
import SearchForm from './components/SearchForm'
import SearchHistory from './components/SearchHistory'
import SearchList from './components/SearchList'
import BScroll from 'better-scroll'
import { setStore, getStore } from '@/config/utils'
import { getSearchList } from '@/ajaxApi/getData'
export default {
	name: 'Serach',
	components: {
		HeaderTop,
		FootGuide,
		SearchForm,
		SearchHistory,
		SearchList
	},
	data () {
		return {
			headTitle: '搜索',
			geohash: '',	// msite页面传递过来的地址信息
			showSearchHistory: false,
			historyList: [],
			showSearchList: false,
			searchList: [],
			inputVal: ''
		}
	},
	methods: {
		// 显示历史搜索列表
		showHistoryList () {
			let history = getStore('searchHistory')
			if (history) {
				// console.log('show')
				history = JSON.parse(history)
				this.historyList = history
				this.showSearchHistory = true
				this.showSearchList = false
			}
		},
		// 隐藏历史搜索列表
		hideHistoryList () {
			this.showSearchHistory = false
		},
		// 根据关键字搜索
		handleSearch (keyword) {
			// console.log(keyword)
			let history = getStore('searchHistory')
			// 储存搜索记录
			if (!history) {		// 第一次存储
				history = []
				history.push(keyword)
				setStore('searchHistory', history)
			} else {	// 储存前先判断是否为重复项
				history = JSON.parse(history)
				let state = true
				history.forEach((item) => {
					if (item === keyword) {
						state = false
					}
				})
				if (state) {
					history.push(keyword)
					setStore('searchHistory', history)
				}
			}
			// 直接点击历史记录时给input赋值
			this.inputVal = keyword
			// 隐藏历史记录
			this.showSearchHistory = false
			// ajax
			getSearchList(this.geohash, keyword).then((res) => {
				res = res.data
				this.searchList = res
				this.showSearchList = true
			})

		}
	},
	mounted () {
		// BScroll
		this.scroll = new BScroll(this.$refs.content, { click: true })
		// init
		this.geohash = this.$route.params.geohash	// 获取地址信息
		this.showHistoryList()	// 默认显示历史搜索
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';

	.content {
		@include scrollMidContent;
	}
</style>


