<template>
	<div class="alert_mask">
		<div class="alert_tip">
			<div class="alert_icon">
				<span class="line"></span>
				<span class="dot"></span>
			</div>
			<p class="alert_text">{{ tipText }}</p>
			<div class="alert_btn" @click="handleClick">确认</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'AlertTip',
	props: {
		tipText: String
	},
	methods: {
		handleClick () {
			this.$emit('closeTip')
		}
	}
}
</script>

<style lang="scss" scoped>
	@keyframes tipMove {
		0% { transform: scale(1) }
		35% { transform: scale(.8) }
		70% { transform: scale(1.1) }
		100% { transform: scale(1) }
	}

	.alert_mask {
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 200;
		.alert_tip {
			animation: tipMove .4s;
			width: 5.6rem;
			padding-top: .28rem;
			position: absolute;
			top: 50%;
			left: 50%;
			/* transform: translate(-50%, -50%); */
			margin-left: -2.8rem;
			margin-top: -2.8rem;
			background: #fff;
			border-radius: .1rem;
			display: flex;
			flex-direction: column;
			align-items: center;
			.alert_icon {
				width: 1.26rem;
				height: 1.26rem;
				border: solid .07rem #f8cb86;
				border-radius: 50%;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				.line {
					width: .06rem;
					height: .7rem;
					background: #f8cb86;
				}
				.dot {
					width: .1rem;
					height: .1rem;
					background: #f8cb86;
					border-radius: 50%;
					margin-top: .1rem;
				}
			}
		}
		.alert_text {
			margin-top: .38rem;
			padding: 0 .2rem;
			line-height: .42rem;
			font-size: .32rem;
			color: #333;
		}
		.alert_btn {
			margin-top: .38rem;
			width: 100%;
			height: .84rem;
			line-height: .84rem;
			text-align: center;
			background: #4cd964;
			font-size: .36rem;
			font-weight: bold;
			color: #fff;
			border-radius: 0 0 .1rem .1rem;
		}
	}
</style>


