<template>
	<div class="buy_cart">
		<!-- 列表中显示 -->
		<div v-if="food.specifications">
			<!-- 列表中不能选规格 -->
			<div v-if="hasSpecsFalse" class="edit_btns">
				<transition name="cut">
					<span v-show="selNums" class="cut_btn" @click.stop="handleCutCart(food.category_id, food.item_id, food.specfoods[0].food_id, food.specfoods[0].name, food.specfoods[0].price, '', food.specfoods[0].packing_fee, food.specfoods[0].sku_id, food.specfoods[0].stock)">
						<svg>
		                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-minus"></use>
		                </svg>
					</span>
				</transition>
				<transition name="fade">
					<span v-show="selNums" class="sel_nums">{{ selNums }}</span>
				</transition>
				<span class="add_btn" @click.stop="handleAddCart(food.category_id, food.item_id, food.specfoods[0].food_id, food.specfoods[0].name, food.specfoods[0].price, '', food.specfoods[0].packing_fee, food.specfoods[0].sku_id, food.specfoods[0].stock, $event)">
					<svg>
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-add"></use>
					</svg>
				</span>
			</div>
			<!-- 列表中可以选规格 -->
			<div v-else class="edit_btns">
				<transition name="cut">
					<span v-show="selNums" class="cut_btn" @click.stop="handleCutCart(null)">
						<svg>
		                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-minus"></use>
		                </svg>
					</span>
				</transition>
				<transition name="fade">
					<span v-show="selNums" class="sel_nums">{{ selNums }}</span>
				</transition>
				<span class="spec_btn" @click.stop="handleSpecClick(food)">选规格</span>
			</div>
		</div>
		<!-- 购物车中显示 -->
		<div v-else class="edit_btns">
			<transition name="cut">
				<span class="cut_btn" @click="handleCutCart(food.category_id, food.item_id, food.food_id, food.name, food.price, food.specs, food.packing_fee, food.sku_id, food.stock)">
					<svg>
	                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-minus"></use>
	                </svg>
				</span>
			</transition>
			<transition name="fade">
				<span class="sel_nums">{{ selNumsInCart }}</span>
			</transition>
			<span class="add_btn" @click="handleAddCart(food.category_id, food.item_id, food.food_id, food.name, food.price, food.specs, food.packing_fee, food.sku_id, food.stock)">
				<svg>
					<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#cart-add"></use>
				</svg>
			</span>
		</div>
	</div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
	name: 'BuyCart',
	props: {
		food: Object
	},
	data () {
		return {
			showMoveDotArr: []
		}
	},
	computed: {
		...mapState(['shopId', 'cartList']),
		// 监听vuex中cartList的变化, 并获取当前商铺的购物车的对象信息(返回一个新对象)
		curShopCart () {
			return Object.assign({}, this.cartList[this.shopId])
		},
		// 已选数量
		selNums () {
			let cate_id = this.food.category_id
			let item_id = this.food.item_id
			let num = 0
			if (this.curShopCart[cate_id] && this.curShopCart[cate_id][item_id]) {
				Object.values(this.curShopCart[cate_id][item_id]).forEach((item) => {
					if (item) {
						num += item.num
					}
				})
				return num
			} else {
				return num
			}
			
		},
		// 已选数量(购物车中)
		selNumsInCart () {
			let cate_id = this.food.category_id
			let item_id = this.food.item_id
			let food_id = this.food.food_id
			return this.curShopCart[cate_id][item_id][food_id].num
		},
		// 判断是否能选择规格
		hasSpecsFalse () {
			return this.food.specifications.length < 1
		}
	},
	methods: {
		...mapMutations(['ADD_CART', 'REDUCE_CART']),
		// 加入购物车
		handleAddCart (category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock, ev) {
			// console.log(arguments)
			let dataObj = {
				shopId: this.shopId,
				category_id, 
				item_id, 
				food_id, 
				name, 
				price, 
				specs, 
				packing_fee, 
				sku_id, 
				stock
			}
			this.ADD_CART(dataObj)
			// 加入购物车的动画效果(在列表中添加)
			if (!this.food.specifications) {
				return
			}
			// 记录icon的位置
			let rect = ev.target.getBoundingClientRect()
			// console.log(rect)
			let iconLeft = rect.left
			let iconBottom = rect.bottom
			// 添加到数组中
			this.showMoveDotArr.push(true)
			this.$emit('showMoveDot', this.showMoveDotArr, iconLeft, iconBottom)
		},
		// 从购物车中移除
		handleCutCart (category_id, item_id, food_id, name, price, specs, packing_fee, sku_id, stock) {
			// 如果可以选规格 则不能直接删除
			if (this.food.specifications && this.food.specifications.length) {
				this.$emit('showReduceTip')
				return
			}
			// 防止用户在列表中快速点击删除按钮导致的bug
			if (this.food.specifications && this.selNums < 1) {
				return
			}
			let dataObj = {
				shopId: this.shopId,
				category_id, 
				item_id, 
				food_id, 
				name, 
				price, 
				specs, 
				packing_fee, 
				sku_id, 
				stock
			}
			this.REDUCE_CART(dataObj)
		},
		// 选择规格
		handleSpecClick (foodObj) {
			this.$emit('showSpecsList', foodObj)
		},
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	
	// fade
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to {
	  opacity: 0;
	}
	// cut
	.cut-enter-active, .cut-leave-active {
	  transition: all .5s;
	}
	.cut-enter, .cut-leave-to {
	  opacity: 0;
	  transform: translateX(100%)
	}

	.edit_btns {
		display: flex;
		align-items: center;
		.cut_btn, .add_btn {
			display: inline-block;
			vertical-align: middle;
			width: .4rem;
			height: .4rem;
			svg {
				width: 100%;
				height: 100%;
				fill: $blueColor;
			}
		}
		.sel_nums {
			margin: 0 .1rem;
		}
		.spec_btn  {
			display: inline-block;
			vertical-align: middle;
			height: .4rem;
			line-height: .4rem;
			padding: 0 .06rem;
			background: $blueColor;
			color: #fff;
			text-align: center;
			border-radius: .06rem;
			font-size: .24rem;
		}
	}
</style>


