<template>
	<div>
		<header-top showLogin="home" :headTitle="headTitle" :showLogin="true">
			<span slot="logo" class="head_logo">eat.me</span>
		</header-top>
		<div class="content" ref="content">
			<div>
				<!-- 城市定位 -->
				<div class="city_nav">
					<div class="desc border-bottom">
						<span class="">当前定位城市:</span>
						<span class="name" v-if="currCity"></span>
						<span class="name" v-else>定位不准时，请在城市列表中选择</span>
					</div>
					<div class="current">
						<span class="name">{{ currCity }}</span>
						<i class="iconfont">&#xe628;</i>
					</div>
				</div>
				<!-- 选择城市列表 -->
				<city-list :hotCities="hotCities" :allCities="allCities" @changeScroll="handleChangeScroll"></city-list>
			</div>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
import BScroll from 'better-scroll'
import HeaderTop from '@/components/header/Header'
import CityList from './components/CityList'
import { getHotCities, getAllCities, getCurrentCities } from '@/ajaxApi/getData'
export default {
	name: 'Home',
	components: {
		HeaderTop,
		CityList
	},
	data () {
		return {
			headTitle: '',
			currCity: '',
			hotCities: [],
			allCities: {}
		}
	},
	methods: {
		handleChangeScroll (char) {
			let dom = document.querySelector('#area' + char)
			this.scroll.scrollToElement(dom)
		} 
	},
	mounted () {
		// 加载BScroll
		this.scroll = new BScroll(this.$refs.content, { click: true })
		// 获取当前城市
		getCurrentCities().then((res) => {
			const data = res.data
			console.log(data)
		})
		// 获取热门城市
		getHotCities().then((res) => {
			const data = res.data
			this.hotCities = data
		})
		// 获取所有城市
		getAllCities().then((res) => {
			const data = res.data
			this.allCities = data
		})
	}
}
</script>

<style lang="scss" scoped>
	@import '~style/mixin';

	.head_logo {
		font-size: .32rem;
		color: #fff;
	}
	.content {
		position: absolute;
		z-index: 1;
		top: .92rem;
		bottom: 0;
		left: 0;
		right: 0;
		.border-bottom {
			&:before {
				border-color: $borderColor;
			}
		}
		.city_nav {
			margin-bottom: .2rem;
			background: #fff;
			.desc, .current {
				height: .8rem;
				line-height: .8rem;
				padding: 0 .2rem;
				display: flex;
				justify-content: space-between;
			}
			.desc {
				.name {
					color: #9f9f9f;
					font-weight: bold;
					font-size: .24rem;
				}
			}
			.current {
				border-bottom: solid 2px $borderColor;
			}
		}
	}
</style>


