<template>
	<div>
		<header-top showBack="true" headTitle="我的"></header-top>
		<div class="content">
			<!-- info -->
			<router-link tag="div" :to="infoTo" class="user_message">
				<div class="icon_wrap">
					<img v-if="userInfo" class="icon_img" :src="imgBaseUrl + userInfo.avatar" alt="">
					<svg v-else class="icon_default">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#avatar-default"></use>
					</svg>
				</div>
				<div class="mes">
					<p class="username">{{ userName }}</p>
					<p class="phone">
						<svg class="phone_icon">
							<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#mobile"></use>
						</svg>
						<span class="phone_number">{{ phoneNumber }}</span>
					</p>
				</div>
				<i class="iconfont arrow">&#xe628;</i>
			</router-link>
			<!-- info入口 -->
			<router-view></router-view>
			<!-- data -->
			<div class="data_list">
				<ul class="clearfix">
					<li class="data_item">
						<p class="data"><b>0.00</b>元</p>
						<p class="desc">我的余额</p>
					</li>
					<li class="data_item">
						<p class="data"><b class="b2">0</b>个</p>
						<p class="desc">我的优惠</p>
					</li>
					<li class="data_item">
						<p class="data"><b class="b3">0</b>分</p>
						<p class="desc">我的积分</p>
					</li>
				</ul>
			</div>
			<!-- other -->
			<div class="other_list">
				<router-link tag="div" to="/order" class="other_item">
					<svg class="other_icon" fill="#4aa5f0">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#order"></use>
					</svg>
					<div class="other_content border-bottom">
						<span>我的订单</span>
						<i class="iconfont arrow">&#xe628;</i>
					</div>
				</router-link>
				<a href="https://home.m.duiba.com.cn/#/chome/index" class="other_item">
					<svg class="other_icon" fill="#fc7b53">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#point"></use>
					</svg>
					<div class="other_content border-bottom">
						<span>积分商城</span>
						<i class="iconfont arrow">&#xe628;</i>
					</div>
				</a>
				<router-link tag="div" to="/vipcard" class="other_item">
					<svg class="other_icon" fill="#ffc636">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#vip"></use>
					</svg>
					<div class="other_content border-bottom">
						<span>饿了么会员卡</span>
						<i class="iconfont arrow">&#xe628;</i>
					</div>
				</router-link>
				<router-link tag="div" to="/service" class="other_item">
					<svg class="other_icon" fill="#4aa5f0">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#service"></use>
					</svg>
					<div class="other_content border-bottom">
						<span>服务中心</span>
						<i class="iconfont arrow">&#xe628;</i>
					</div>
				</router-link>
				<router-link tag="div" to="/download" class="other_item">
					<svg class="other_icon" fill="#3cabff">
						<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#download"></use>
					</svg>
					<div class="other_content border-bottom">
						<span>下载饿了么APP</span>
						<i class="iconfont arrow">&#xe628;</i>
					</div>
				</router-link>
			</div>
		</div>
		<!-- 底部 -->
		<foot-guide></foot-guide>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
import FootGuide from '@/components/footer/FootGuide'
import AlertTip from '@/components/common/AlertTip'
import { mapState } from 'vuex'
export default {
	name: 'Profile',
	components: {
		HeaderTop,
		FootGuide,
		AlertTip
	},
	data () {
		return {
			imgBaseUrl: '//elm.cangdu.org/img/'
		}
	},
	computed: {
		...mapState(['userInfo']),
		// 用户名
		userName () {
			let username = ''
			if (this.userInfo) {
				username = this.userInfo.username
			} else {
				username = '登录/注册'
			}
			return username
		},
		// 电话号码
		phoneNumber () {
			let phonenum = ''
			if (this.userInfo) {
				phonenum = this.userInfo.mobile || '暂无绑定手机号'
			} else {
				phonenum = '未登录'
			}
			return phonenum
		},
		// info跳转地址
		infoTo () {
			let infoUrl = ''
			if (this.userInfo) {
				infoUrl = '/profile/info'
			} else {
				infoUrl = '/login'
			}
			return infoUrl
		}
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	.border-bottom:before {
		border-color: $borderColor;
	}

	.content {
		padding-top: .92rem;
		.user_message {
			padding: .32rem .28rem;
			background: $blueColor;
			display: flex;
			align-items: center;
			.icon_wrap {
				width: 1.16rem;
				height: 1.16rem;
				border-radius: 50%;
				overflow: hidden;
				.icon_img, .icon_default {
					width: 100%;
					height: 100%;
				}
				.icon_img {
					display: block;
				}
				.icon_default {
					background: #fff;
				}
			}
			.mes {
				min-width: 0;
				flex: 1;
				padding-left: .24rem;
				p {
					height: .46rem;
					line-height: .46rem;
					color: #fff;
					@include ellipsis;
				}
				.username {
					font-size: .36rem;
					font-weight: bold;
				}
				.phone {
					display: flex;
					align-items: center;
					.phone_icon {
						width: .24rem;
						height: .36rem;
						fill: #fff;
					}
					.phone_number {
						margin-left: .1rem;
					}
				}

			}
			.arrow {
				color: #fff;
			}
		}
		.data_list {
			margin-bottom: .2rem;
			.data_item {
				float: left;
				width: 33.33%;
				border-right: solid 1px #f1f1f1;
				background-color: #fff;
				text-align: center;
				.data {
					padding: .4rem 0 .2rem 0;
					b {
						font-size: .56rem;
						font-weight: 700;
						color: #f90;
						line-height: 1;	
					}
					b.b2 {
						color: #ff5f3e;
					}
					b.b3 {
						color: #6ac20b;
					}
				}
				.desc {
					line-height: .36rem;
					padding-bottom: .2rem;
				}
			}
			.data_item:last-child {
				border-right: none;
			}
		}
		.other_list {
			.other_item {
				background: #fff;
				padding-left: .4rem;
				height: .84rem;
				display: flex;
				align-items: center;
				.other_icon {
					width: .32rem;
					height: .32rem;
					margin-right: .1rem;
				}
				.other_content {
					height: 100%;
					flex: 1;
					display: flex;
					justify-content: space-between;
					align-items: center;
					padding-right: .1rem;
					span {
						font-size: .32rem;
						color: #333;
					}
					.arrow {
						fill: #bbb;
					}
				}
			}
			.other_item:nth-child(3) {
				margin-bottom: .2rem;
			}
		}
	}
</style>


