<template>
	<div>
		<div v-if="showList">
			<h4 class="title">商家</h4>
			<ul>
				<li class="item border-bottom" v-for="item in list" :key="item.id">
					<div class="pic">
						<img :src="imgBaseUrl + item.image_path" alt="">
					</div>
					<div class="desc">
						<div class="desc_in_wrap border-bottom">
							<div class="desc_in">
								<span class="name">{{ item.name }}</span>
								<svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="24" height="14" class="pay_icon">
                                    <polygon points="0,14 4,0 24,0 20,14" style="fill:none;stroke:#FF6000;stroke-width:1" />
                                    <line x1="1.5" y1="12" x2="20" y2="12" style="stroke:#FF6000;stroke-width:1.5"/>
                                    <text x="3.5" y="9" style="fill:#FF6000;font-size:9;font-weight:bold;">支付</text>
                                </svg>
							</div>
							<div class="desc_in">
								月售 {{ item.recent_order_num }} 单
							</div>
							<div class="desc_in">
								{{ item.float_minimum_order_amount }} 元起送 / 距离{{ item.distance }}
							</div>
						</div>
						<!-- 优惠 -->
						<ul class="offer">
							<li 
								class="offer_item" 
								v-for="offer in item.activities"
								:key="offer.id"
							>
								<span class="icon_text" :style="{ backgroundColor: `#${offer.icon_color}` }">{{ offer.icon_name }}</span>
								{{ offer.name }}
								<span class="remark">({{ offer.description }})</span>
							</li>
						</ul>
					</div>
				</li>
			</ul>
		</div>
		<div v-else class="tips">很抱歉! 无搜索结果</div>
	</div>
</template>

<script>
export default {
	name: 'SearchList',
	props: {
		list: Array
	},
	data () {
		return {
			imgBaseUrl: '//cangdu.org:8001/img/'
		}
	},
	computed: {
		showList () {
			return this.list.length
		}
	},
	mounted () {
		// console.log(this.list)
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	.border-bottom:before {
		border-color: $borderColor;
	}

	.title {
		height: .92rem;
		line-height: .92rem;
		padding-left: .24rem;
	}
	.item {
		background: #fff;
		display: flex;
		padding: .24rem .24rem .36rem .24rem;
		.pic {
			width: .8rem;
			height: .8rem;
			overflow: hidden;
			margin-right: .1rem;
			img {
				width: 100%;
			}
		}
		.desc {
			flex: 1;
			.desc_in_wrap {
				padding-bottom: .1rem;
				.desc_in {
					line-height: .4rem;
					color: #333;
					.pay_icon {
						margin-bottom: -.04rem;
					}
				}
			}
			.offer {
				margin-top: .06rem;
				.offer_item {
					line-height: .4rem;
					@include ellipsis;
					font-size: .24rem;
					.icon_text {
						display: inline-block;
						height: .32rem;
						line-height: .32rem;
						padding: 0 .02rem;
						border-radius: .06rem;
						color: #fff;
					}
					.remark {
						color: #FF6000;
					}
				}
			}
		}
	}
	.tips {
		background: #fff;
		height: .8rem;
		line-height: .8rem;
		text-align: center;
		color: #333;
		margin-top: .1rem;
	}
</style>


