<template>
	<transition>
		<div class="showBack" @click="backTop">
			<svg class="back_svg">
				<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#backtop"></use>
			</svg>
		</div>
	</transition>
</template>

<script>
export default {
	name: 'ShowBack',
	methods: {
		// 回到顶部
		backTop () {
			this.$emit('back')
		}
	}
}
</script>

<style lang="scss" scoped>
	.v-enter, .v-leave-to {
		opacity: 0;
	}
	.v-enter-active, .v-leave-active {
		transition: opacity .6s;
	}
	.showBack {
		position: fixed;
		z-index: 10;
		right: .4rem;
		bottom: 1.32rem;
		.back_svg {
			width: .8rem;
			height: .8rem;
		}
	}
</style>


