<template>
	<div class="search">
		<form class="search_form border-top" @submit.prevent>
			<input type="search" class="search_input" placeholder="请输入关键字" required="" v-model="inputVal">
			<button type="submit" class="search_submit" @click="searchSubmit">提交</button>
		</form>
	</div>
</template>

<script>
export default {
	name: 'CitySerach',
	data () {
		return {
			inputVal: ''
		}
	},
	methods: {
		searchSubmit () {
			if (this.inputVal) {
				this.$emit('search', this.inputVal)
			}
		}
	},
	mounted () {
		
	}
}
</script>

<style lang="scss" scoped>
	@import '~style/mixin';
	.border-top {
		&:before {
			border-color: $borderColor;
		}
	}
	.search {
		margin-top: .16rem;
		.search_form {
			background: #fff;
			border-bottom: solid 2px $borderColor;
			padding: .2rem .3rem;
			.search_input {
				display: block;
				width: 100%;
				height: .64rem;
				padding: 0 .16rem;
				color: #666;
				border: solid 1px $borderColor;
				border-radius: .06rem;
				margin-bottom: .2rem;
			}
			.search_submit {
				display: block;
				width: 100%;
				height: .64rem;
				line-height: .64rem;
				background: #3190e8;
				border-radius: .06rem;
				text-align: center;
				color: #fff;
				font-size: .32rem;
			}
		}
	}
</style>


