<template>
	<div class="search_form">
		<input 
			type="search" 
			class="search_input" 
			placeholder="请输入商家或美食名称" 
			v-model="searchVal" 
			@input="checkInput"
			@keyup.enter="searchSubmit"
		>
		<span class="search_submit" @click="searchSubmit">提交</span>
	</div>
</template>

<script>
export default {
	name: 'SearchForm',
	props: {
		inputVal: String
	},
	data () {
		return {
			searchVal: ''
		}
	},
	watch: {
		inputVal (val, old) {
			// console.log(val, old)
			this.searchVal = this.inputVal
		}
	},
	methods: {
		searchSubmit () {
			if (!this.searchVal) {
				return
			}
			this.$emit('search', this.searchVal)
		},
		checkInput () {
			if (!this.searchVal) {
				this.$emit('history')
			}
		}
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';

	.search_form {
		background: #fff;
		padding: .24rem;
		display: flex;
		.search_input {
			flex: 1;
			border: solid 1px $borderColor;
			border-radius: .04rem;
			color: #333;
			font-weight: bold;
			padding: 0 .1rem;
			background: #f2f2f2;
		}
		.search_submit {
			width: 1.5rem;
			height: .7rem;
			line-height: .7rem;
			border-radius: .04rem;
			background: $blueColor;
			color: #fff;
			text-align: center;
			margin-left: .1rem;
		}
	}
</style>


