<template>
	<div>
		<!-- 热门城市 -->
		<div class="area">
			<div class="title">热门城市</div>
			<ul class="list hot">
				<li v-for="item in hotCities" :key="item.id" @click="handleCityClick(item.id)">{{ item.name }}</li>
			</ul>
		</div>
		<!-- 字母排序 -->
		<div class="area">
			<div class="title">字母排序</div>
			<ul class="list noBor">
				<li v-for="(item, key) in sortedList" :key="'char' + key" @click="handleCharClick(key)">{{ key }}</li>
			</ul>
		</div>
		<!-- 城市列表 -->
		<div class="area" v-for="(item, key) in sortedList" :key="key" :id="'area' + key">
			<div class="title">{{ key }} <span class="desc" v-if="key === 'A'">(按字母排序)</span></div>
			<ul class="list">
				<li v-for="innerItem in item" :key="innerItem.id" @click="handleCityClick(innerItem.id)">{{ innerItem.name }}</li>
			</ul>
		</div>
	</div>
</template>

<script>
export default {
	name: 'CityList',
	props: {
		hotCities: Array,
		allCities: Object
	},
	data () {
		return {

		}
	},
	computed: {
		// 将获取的数据按照A-Z字母开头排序
		sortedList () {
			let sortObj = {}
			for (let i = 65; i <= 90; i++) {
				if (this.allCities[String.fromCharCode(i)]) {
					sortObj[String.fromCharCode(i)] = this.allCities[String.fromCharCode(i)]
				}
			}
			return sortObj
		}
	},
	methods: {
		// 按字母搜索
		handleCharClick (char) {
			this.$emit('changeScroll', char)
		},
		// 点击城市
		handleCityClick (cityId) {
			console.log(cityId)
			// 跳转至城市页
			this.$router.push('/city/' + cityId)
		}
	}
}
</script>

<style lang="scss" scoped>
	@import '~style/mixin';

	.area {
		background: #fff;
		margin-bottom: .2rem;
		.title {
			line-height: .68rem;
			padding: 0 .2rem;
			border-top: solid 2px $borderColor;
			border-bottom: solid 1px $borderColor;
			.desc {
				color: #999;
				font-size: .24rem;
			}
		}
		.list {
			overflow: hidden;
			border-bottom: solid 1px $borderColor;
			li {
				float: left;
				width: 25%;
				padding: 0 .1rem;
				line-height: .8rem;
				text-align: center;
				border-right: solid 1px $borderColor;
				border-bottom: solid 1px $borderColor;
				@include ellipsis;
			}
			li:nth-of-type(4n) {
				border-right: none;
			}
		}
		.list.hot {
			li {
				color: $blueColor;
			}
		}
	}
</style>


