<template>
	<div class="">
		<header-top showBack="true" :headTitle="headTitle"></header-top>
		<!-- 筛选 -->
		<food-filter 
			v-if="hasGetData"
			:geohash="geohash"
			:categoryName="headTitle"
			:restaurantCategoryId="restaurantCategoryId"
			@filterCate="handleFilterData"
		>
		</food-filter>
		<!-- scroll -->
		<div class="content" ref="content">
			<div>
				<!-- list -->
				<shop-list
					v-if="hasGetData"
					:geohash="geohash"
					:restaurantCategoryId="restaurantCategoryId"
					:restaurantCategoryIds="restaurantCategoryIds"
					:sortByType="sortByType"
					:supportWays="supportWays"
					:hasToBot="hasToBot"
					@changeLoading="handleChangeLoading"
					@refresh="handleRefreshScroll"
				></shop-list>
			</div>
		</div>
		<!-- loading动画 -->
		<Loading v-if="showLoading"></Loading>
		<!-- 列表(shop-list)返回顶部按钮 -->
		<show-back v-show="showBack" @back="handleBackTop"></show-back>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
import FoodFilter from './components/FoodFilter'
import Loading from '@/components/common/Loading'
import ShopList from '@/components/common/ShopList'
import ShowBack from '@/components/common/ShowBack'
import BScroll from 'better-scroll'
export default {
	name: 'Food',
	components: {
		HeaderTop,
		FoodFilter,
		BScroll,
		Loading,
		ShopList,
		ShowBack
	},
	data () {
		return {
			hasGetData: false,
			geohash: '',
			headTitle: '',
			restaurantCategoryId: '',	// 分类id
			restaurantCategoryIds: '',	// 分类id下的子类id
			sortByType: '',
			supportWays: null,
			showBack: false,
			hasToBot: false,
			showLoading: true
		}
	},
	computed: {
		
	},
	methods: {
		handleBackTop () {
			// console.log(this.scroll)
			this.scroll.scrollTo(0, 0, 600)
		},
		handleChangeLoading (state) {
			this.showLoading = state
		},
		handleRefreshScroll () {
			this.scroll.refresh()
		},
		// 重新筛选数据
		handleFilterData (res) {
			console.log(res)
			if (res.cateName) {
				this.headTitle = res.cateName
			}
			// this.restaurantCategoryId = res.cateId
			if (res.cateIds) {
				this.restaurantCategoryIds = res.cateIds
			}
			if (res.sortByType !== '' && res.sortByType !== undefined) {
				this.sortByType = res.sortByType
			}
			if (res.supportWays) {
				this.supportWays = res.supportWays
			}
		}
	},
	mounted () {
		// 引入better-scroll
		this.scroll = new BScroll(this.$refs.content, {
			click: true,
			probeType: 3,	//探针, 获取滑动距离必备
		})
		// 监听BScroll的滚动
		this.scroll.on('scroll', (pos) => {
			// console.log(this.scroll)
			if (pos.y <= -500) {
				this.showBack = true
			} else {
				this.showBack = false
			}

			// 监听是否滚动到底部
			if (pos.y < this.scroll.maxScrollY) {
				// console.log(this.hasToBot)
				this.hasToBot = true
			} else {
				this.hasToBot = false
			}
		})
		// 获取url query参数
		this.geohash = this.$route.query.geohash
		this.headTitle = this.$route.query.title
		this.restaurantCategoryId = this.$route.query.restaurant_category_id
		// 获取参数成功后再渲染子组件
		this.hasGetData = true
	}
}	
</script>

<style lang="scss" scoped>
	.content {
		overflow: hidden;
		position: absolute;
		z-index: 1;
		top: 1.67rem;
		bottom: 0;
		left: 0;
		right: 0;
	}
</style>


