<template>
	<div>
		<header-top showBack="true" headTitle="密码登录"></header-top>
		<div class="login_content">
			<!-- 输入区域 -->
			<form class="login_form">
				<div class="input_wrap border-bottom">
					<input type="text" placeholder="账号" v-model.lazy.trim="userAccount">
				</div>
				<div class="input_wrap border-bottom">
					<input v-if="showPassword" type="password" placeholder="密码" v-model="password">
					<input v-else type="text" placeholder="密码" v-model="password">
					<!-- 密码可见设置 -->
					<div class="button_switch" :class="{ active: !showPassword }">
						<div class="cycle" :class="{ to_right: !showPassword }" @click="changePassType"></div>
						<span>密码可见...</span>
					</div>
				</div>
				<div class="input_wrap border-bottom">
					<input type="text" placeholder="验证码" v-model="verifyCode">
					<div class="change_code_img">
						<img v-show="codeImgUrl" :src="codeImgUrl" alt="">
						<div class="change_code" @click="handleGetVerifyCode">
							<p>看不清</p>
							<p>换一张</p>
						</div>
					</div>
				</div>
			</form>
			<!-- 提示与登录 -->
			<p class="login_tips">温馨提示：未注册过的账号，登录时将自动注册</p>
			<p class="login_tips">注册过的用户可凭账号密码登录</p>
			<div class="submit_btn" @click="handleLogin">登录</div>
			<p class="to_forget_p">
				<router-link to="/forget" class="to_forget">重置密码?</router-link>
			</p>
		</div>
		<!-- 提示弹框 -->
		<alert-tip v-if="showAlertTip" @closeTip="handleCloseTip" :tipText="alertTipText"></alert-tip>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
import AlertTip from '@/components/common/AlertTip'
import { getVerifyCode, accountLogin } from '@/ajaxApi/getData'
import { mapMutations } from 'vuex'
export default {
	name: 'Login',
	components: {
		HeaderTop,
		AlertTip
	},
	data () {
		return {
			userAccount: '',
			showPassword: true,
			password: '',
			verifyCode: '',
			codeImgUrl: null,
			showAlertTip: false,
			alertTipText: '',
			userInfo: null	// 获取到的用户信息
		}
	},
	methods: {
		...mapMutations(['RECORD_USERINFO']),
		changePassType () {
			this.showPassword = !this.showPassword;
		},
		// 获取验证吗，线上环境使用固定的图片，生产环境使用真实的验证码
		async handleGetVerifyCode () {
			let codeImg = await getVerifyCode()
			codeImg = codeImg.data.code
			this.codeImgUrl = codeImg
		},
		// 登录/注册
		async handleLogin () {
			if (!this.userAccount) {
				this.showAlertTip = true
				this.alertTipText = '请输入手机号/邮箱/用户名'
				return
			}
			if (!this.password) {
				this.showAlertTip = true
				this.alertTipText = '请输入密码'
				return
			}
			if (!this.verifyCode) {
				this.showAlertTip = true
				this.alertTipText = '请输入验证码'
				return
			}
			// 用户名登录
			let res = await accountLogin(this.userAccount, this.password, this.verifyCode)
			res = res.data
			console.log(res)
			this.userInfo = res
			if (!this.userInfo.user_id) {	// 登录失败
				this.showAlertTip = true
				this.alertTipText = this.userInfo.message
				this.handleGetVerifyCode()	// 更新验证码
				return
			}
			// 登录成功
			this.RECORD_USERINFO(this.userInfo)		// 记录登录人信息
			this.$router.go(-1)
		},
		// 关闭提示弹框
		handleCloseTip () {
			this.showAlertTip = false
			this.alertTipText = ''
		}
	},
	created () {
		// 获取验证码
		this.handleGetVerifyCode()
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	.border-bottom {
		&:before {
			border-color: $borderColor;
		}
	}

	.login_content {
		padding-top: 1.12rem;
		.login_form {
			background: #fff;
			.input_wrap {
				padding: .28rem .38rem;
				display: flex;
				justify-content: space-between;
				input {
					// width: 100%;
					font-size: .32rem;
				}
				.button_switch {
					padding: 0 .1rem;
					background: #ccc;
					color: #fff;
					font-size: .24rem;
					height: .32rem;
					line-height: .32rem;
					border-radius: .16rem;
					position: relative;
					.cycle {
						position: absolute;
						top: -.12rem;
						left: -.2rem;
						width: .56rem;
						height: .56rem;
						background: #f1f1f1;
						border-radius: 50%;
						// box-shadow: 0 .04rem .08rem 0 rgba(0, 0, 0, .1);
						transition: left .5s;
					}
					.cycle.to_right {
						left: .9rem;
					}
				}
				.button_switch.active {
					background: #4cd964;
				}
				.change_code_img {
					display: flex;
					align-items: center;
					height: .38rem;
					img {
						width: 1.64rem;
						height: .7rem;
						margin-right: .1rem;
					}
					.change_code {
						p {
							font-size: .24rem;
						}
						p:nth-child(2) {
							color: $blueColor;
							margin-top: .1rem;
						}
					}
				}
			}
		}
		.login_tips {
			padding: .18rem .28rem;
			font-size: .24rem;
			color: red;
			line-height: 1;
		}
		.submit_btn {
			margin: 0 .24rem .48rem .24rem;
			padding: .24rem 0;
			line-height: .42rem;
			border-radius: .06rem;
			background: #4cd964;
			font-size: .32rem;
			color: #fff;
			text-align: center;
		}
		.to_forget_p {
			padding-right: .24rem;
			text-align: right;
			.to_forget {
				color: #3b95e9;
			}
		}
	}
</style>


