import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

// state
const state = {
	geohash: '',	// 当前位置纬度 + 经度
	latitude: '',	// 当前位置纬度
	longitude: '',	// 当前位置经度
	cartList: {},	// 加入购物车的商品列表
	shopId: null,	// 商铺id
	userInfo: null,	// 登录人信息
	login: false,	// 登录状态
}

export default new Vuex.Store({
	state,
	mutations,
	actions
})


