<template>
	<div>
		<div class="shop_container">
			<shop-header 
				v-if="shopDetailsData"
				:shopDetailsData="shopDetailsData"
			></shop-header>
			<!-- 商品与评价的切换 -->
			<div class="switch_type border-bottom">
				<div class="item" :class="{ active: showTypeState === 1 }" @click="handleSwitchType(1)">
					<span class="text">商品</span>
				</div>
				<div class="item" :class="{ active: showTypeState === 2 }" @click="handleSwitchType(2)">
					<span class="text">评价</span>
				</div>
			</div>
			<!-- content -->
			<div class="content">
				<!-- 商品列表 -->
				<transition>
					<shop-detail-list 
						v-if="showTypeState === 1 && hasGetData"
						:shopLeftMenu="shopLeftMenu"
						:shopDetailsData="shopDetailsData"
					></shop-detail-list>
				</transition>
				<!-- 评价 -->
				<transition>
					<shop-evaluate v-if="showTypeState === 2"></shop-evaluate>
				</transition>
			</div>
		</div>
		<!-- 商品列表详情页的入口 -->
		<transition name="slide">
			<router-view></router-view>
		</transition>
		<!-- 加载动画 -->
		<loading v-if="showLoading"></loading>
	</div>
</template>

<script>
import Loading from '@/components/common/Loading'
import ShopHeader from './components/ShopHeader'
import ShopDetailList from './components/ShopDetailList'
import ShopEvaluate from './components/ShopEvaluate'
import { getMsiteAddress, getShopDetails, getFoodMenu } from '@/ajaxApi/getData'
import { mapState, mapMutations } from 'vuex'
export default {
	name: 'Shop',
	components: {
		Loading,
		ShopHeader,
		ShopDetailList,
		ShopEvaluate
	},
	data () {
		return {
			showLoading: true,
			geohash: '',
			shopId: '',
			shopDetailsData: null,	// 商铺详情信息
			showTypeState: 1,
			shopLeftMenu: null,
			hasGetData: false	// 已成功获取基本数据
		}
	},
	computed: {
		...mapState(['latitude', 'longitude'])
	},
	methods: {
		...mapMutations(['RECORD_ADDRESS', 'SAVE_SHOPID']),
		// 初始化数据
		async initData () {
			console.log(this.latitude, this.longitude)
			// 页面刷新重新获取位置信息
			if (!this.latitude) {
				// ajax
				let res = await getMsiteAddress(this.geohash)
				res = res.data
				// 储存到vuex
				this.RECORD_ADDRESS(res)
			}
			// 商铺id储存到vuex
			this.SAVE_SHOPID(this.shopId)
			// 获取商铺详情信息
			let res1 = await getShopDetails(this.shopId, this.latitude, this.longitude)
			res1 = res1.data
			this.shopDetailsData = res1
			// 获取商铺食品列表信息
			let res2 = await getFoodMenu(this.shopId)
			res2 = res2.data
			this.shopLeftMenu = res2

			// 获取数据成功
			this.hasGetData = true
			this.showLoading = false
		},
		// 切换页面显示
		handleSwitchType (num) {
			this.showTypeState = num
		}
	},
	created () {
		// 获取url query的参数
		this.geohash = this.$route.query.geohash
		this.shopId = this.$route.query.id
		// 网页初始化时从本地缓存获取购物车数据
		
	},
	mounted () {
		this.initData()
	}
}
</script>

<style lang="scss" scoped>
	@import '~@/style/mixin';
	
	// 渐隐渐出
	.v-enter {
		opacity: 0;
	}
	.v-enter-active {
		transition: opacity 1s;
	}
	// 页面从右侧滑出
	.slide-enter {
		transform: translateX(100%);
		opacity: 0;
	}
	.slide-enter-active {
		transition: all .6s linear;
	}

	.border-bottom:before {
		border-color: $borderColor;
	}
	
	// content外层容器
	.shop_container {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
	}

	.switch_type {
		height: 1.06rem;
		background: #fff;
		display: flex;
		position: relative;
    	z-index: 10;
		.item {
			padding-top: .18rem;
			flex: 1;
			text-align: center;
			.text {
				display: inline-block;
				line-height: .6rem;
				padding: 0 .04rem;
				border-bottom: solid .06rem transparent;
				font-size: .3rem;
			}
		}
		.item.active {
			.text {
				border-color: $blueColor;
				color: $blueColor;
			}
		}
	}

	.content {
		flex: 1;
		display: flex;
		overflow: hidden;
	}

</style>


