<template>
	<div class="profile_info">
		<header-top showBack="true" headTitle="账户信息"></header-top>
		<div class="content">
			<div class="head_portrait">
				<input type="file" class="input_file">
				<span class="text">头像</span>
				<div class="portrait_show">
					<img class="portrait_img" src="" alt="">
					<i class="iconfont arrow">&#xe628;</i>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import HeaderTop from '@/components/header/Header'
export default {
	name: 'ProfileInfo',
	components: {
		HeaderTop
	}
}
</script>

<style lang="scss" scoped>
	.profile_info {
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 150;
		background: #f2f2f2;
		.content {
			padding-top: 1.12rem;
			.head_portrait {
				background: #fff;
				border-top: solid 1px #ddd;
				border-bottom: solid 1px #ddd;
				padding: .24rem .18rem;
				display: flex;
				justify-content: space-between;
				position: relative;
				.input_file {
					position: absolute;
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
					width: 100%;
					opacity: 0;
				}
				.text {
					color: #333;
				}
				.portrait_show {

				}
			}
		}
	}
</style>


